<template>
	<div>
		<nav-header></nav-header>
		
		<div class="container">
			<h1>忘记密码?</h1>
			<input type="email" name="email" placeholder="邮箱" />
			<footer class="footer2">
				<success-modal title="发送邮箱成功">重置我的密码</success-modal>
			</footer>
		</div>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader5.vue'
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {}
		},
		components: {
			NavHeader,
			SuccessModal
		},
		mounted() {
			$(".container").css('height', window.innerHeight-40)
		}
	}
</script>

<style scoped="scoped">
.container {
	color: white;
	padding: 30px 20px ;
	background-color: #0087e0;
}
.container h1 {
    font-size: 24px;
    font-weight: bold;
}
.container input {
	width: 100%;
    border: 0;
    background-color: transparent;
    color: white;
    margin-top: 45px;
    padding-bottom: 17px;
    margin-bottom: 38px;
    border-bottom: 1px solid white;
    font-size: 14px;
}
input::-webkit-input-placeholder { /* WebKit browsers */ 
	color: white; 
} 
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
	color: white; 
} 
input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
	color: white; 
} 
input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
	color: white; 
}

</style>


