<template>
	<div style="background-color: white;">
		<nav-header text2='忘记密码?' pathname="forgetPwd"></nav-header>
		
		<div class="container">
			<h1>登陆</h1>
			<input type="text" placeholder="用户名或邮箱" name="username" autofocus="autofocus" @keyup.enter="input" @keyup.delete="del" />
			<input type="password" placeholder="密码" name="pwd"  @keyup.enter="input" @keyup.delete="del" />
			<footer class="btn-login">
				<success-modal title="登陆成功" v-on:todo="todo()">登陆</success-modal>
			</footer>
		</div>
		
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader5.vue'
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {}
		},
		components: {
			NavHeader,
			SuccessModal
		},
		mounted() {
			$(".container").css('height', window.innerHeight-40)
		},
		methods: {
			todo() {
				this.$router.push({name: 'Home'})
			},
			input(event) {
				$(event.target).next().focus();
			},
			del(event) {
				// 功能：一直按着delete键向上删除
				if(event.target.value == "") {
					$(event.target).prev().focus();
				}
			}
		}
	}
</script>

<style scoped="scoped">
.container {
	padding: 70px 30px 0;
	box-sizing: border-box;
	background-color: #0087e0;
	color: white;
	font-family: "黑体";
}
.container h1 {
	font-size: 24px;
	font-weight: bold;
	padding-bottom: 60px;
}
.container input {
	width: 100%;
    border: 0;
    background-color: transparent;
    color: white;
    padding-bottom: 17px;
    margin-bottom: 38px;
    border-bottom: 1px solid white;
    font-size: 14px;
}
input::-webkit-input-placeholder { /* WebKit browsers */ 
	color: white; 
} 
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
	color: white; 
} 
input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
	color: white; 
} 
input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
	color: white; 
}

</style>


