function checkInput () {
var flag = true;
	$('input').keyup(function() {
		$('input').each(function(index, item) {
//			alert($('input:eq('+index+')').val())
			if(!$('input:eq('+index+')').val()) {
				flag = false;
			}else {
				flag = true;
			}
//			alert(flag);
		})
		if(flag) {
			$('footer a').css("background-color","#0689d7");		
		}
	})
	
}

export {checkInput}



