<template>
	<div id="ProjectBudget" style="background-color: white;">
		<nav-header title="项目预算" ></nav-header>
		<hr />
		
		<div class="container">
			<h1>对于这个项目您有多少预算？</h1>
			<ul>
				<li class="mui-clearfix" @click="toProjectDescription">
					<p class="mui-pull-left">{{symbol}}100 - 1K</p>
					<img src="../../assets/img/icon-right3.png" class="icon-right" />
				</li>
				<li class="mui-clearfix" @click="toProjectDescription">
					<p class="mui-pull-left">{{symbol}}1K - 10K</p>
					<img src="../../assets/img/icon-right3.png" class="icon-right" />
				</li>
				<li class="mui-clearfix" @click="toProjectDescription">
					<p class="mui-pull-left">{{symbol}}10K - 100K</p>
					<img src="../../assets/img/icon-right3.png" class="icon-right" />
				</li>
				<li class="mui-clearfix" @click="toProjectDescription">
					<p class="mui-pull-left">{{symbol}}100K - 1000K</p>
					<img src="../../assets/img/icon-right3.png" class="icon-right" />
				</li>
			</ul>
			
			<button @click="show">
				<span class="mui-pull-left">我的货币</span>
				<span class="mui-pull-right">{{coin}}</span>
			</button>
			<div class="chooseDiv">
				<p class="choose" @click="choose">人民币</p>
				<p @click="choose">港币</p>
				<p @click="choose">美元</p>
				<p @click="choose">日元</p>
			</div>
		</div>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader4.vue'
	export default {
		data() {
			return {
				coin: "人民币",
				coin_symbol: [
					{ name: '人民币', symbol: 'CNY￥'},
					{ name: '港币', symbol: 'HK$'},
					{ name: '美元', symbol: '$'},
					{ name: '日元', symbol: 'JPY￥'}
				],
				symbol: "￥",
			}
		},
		components: {
			NavHeader
		},
		methods: {
			toProjectDescription() {
				this.$router.push({name: 'projectDescription'});
			},
			show() {
				$('.chooseDiv').slideToggle();
			},
			choose(event) {
				$(event.target).addClass('choose').siblings().removeClass('choose');
				this.coin = event.target.innerText;
				this.coin_symbol.forEach( (item, index) => {
					if(event.target.innerText == item.name) {
						this.symbol = item.symbol;
					}
				})
				$('.chooseDiv').slideUp();
			}
			
		}
	}
</script>

<style scoped="scoped">
hr {
	position: fixed;
    width: 50%;
    height: 2px;
    border: 0;
    background: #009cdd;
    top: 32px;
}
.container {
	padding: 50px 25px;
}
.container h1 {
    font-size: 24px;
    font-weight: bold;
    padding-top: 45px;
    line-height: 40px;
	padding-bottom: 20px;
    border-bottom: 1px solid #efefef;
}
.container li {
	padding: 21px 0;
    border-bottom: 1px solid #efefef;
}
.container li:last-child {
	border-bottom: 0;
}
li p {
    font-size: 14px;
}
button {
	width: 100%;
	height: 54px;
	border: 1px solid #ececec;
	background-color: white;
	padding: 0 10px;
}
button span:first-child {
	font-size: 16px;
}
button:focus {
	outline: none;
}
button span:last-child {
	font-size: 16px;
	color: #0b87cf;
	font-weight: bold;
}
li {
	list-style: none;
}
.chooseDiv {
    border: 1px solid #eee;
    margin-top: -2px;
    display: none;
}
.chooseDiv p {
	width: 100%;
	height: 40px;
	text-align: center;
	line-height: 40px;
	border-bottom: 1px solid #eee;
	font-size: 14px;
    font-weight: bold;
    color: #0087e0;
}
.chooseDiv p.choose {
	background-color: #addaf6;
	color: white;
}
</style>


