<template>
	<div id="ProjectDescription" style="background-color: white;">
		<nav-header title="项目说明" next="true">
	        <span v-if="next" v-show="disabled" class="disabled">聘请</span>
	        <span v-if="next" v-show="!disabled" @click="next"><success-modal title="聘请成功">聘请</success-modal></span>
		</nav-header>
		<hr />
		
		<div class="container">
			<p class="title">项目描述</p>
			<textarea autoHeight="true" class="neirong" @keyup="check" placeholder="向他介绍您的项目详情，项目需求截至日期········" ></textarea>
			<footer :class="{'btn-disabled': disabled}">
				<a href="javascript:;">聘请</a>
			</footer>
		</div>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader4.vue'
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {
				disabled: true,
			}
		},
		components: {
			NavHeader,
			SuccessModal
		},
		computed: {
			
		},
		mounted() {
			// textarea高度自适应
			$.fn.autoHeight = function(){    
		        function autoHeight(elem){
		            elem.style.height = 'auto';
		            elem.scrollTop = 0; //防抖动
		            elem.style.height = elem.scrollHeight + 'px';
		        }
		        this.each(function(){
		            autoHeight(this);
		            $(this).on('keyup', function(){
		                autoHeight(this);
		            });
		        });     
		    }                
		    $('textarea[autoHeight]').autoHeight();    
		},
		methods: {
			next() {
				this.$router.push({name: ''});
			},
			check() {
				var value = $('textarea').val();
				var isTrue = true;
				if(value != "") {
					isTrue = false;
				}else {
					isTrue = true;
				}
				this.disabled = isTrue;
			},
		}
	}
</script>

<style scoped="scoped">
.disabled {
	color: #d9d9d9;
}
hr {
	position: absolute;
    width: 75%;
    height: 2px;
    border: 0;
    background: #009cdd;
    top: 32px;
}
.container {
	padding: 80px 25px 0;
}
.container .title {
	font-size: 25px;
    font-weight: bold;
}
.container p:focus {
	outline: 0;
}

.neirong {
	padding: 20px 10px 0 0;
	line-height: 20px;
    font-size: 14px;
    word-break: break-all;
    border: 0; 
    width: 100%; 
    resize: none;
    font-family: "黑体";
}
.neirong:focus {
	outline: none;
}
footer a {
	width: 100%;
	height: 56px;
	background-color: #0087E0;
	display: block;
	font-size: 21px;
	color: white;
	text-align: center;
	line-height: 56px;
	font-family: "黑体";
	border-radius: 5px;
}
.btn-disabled a {
	opacity: 0.4;
}
</style>


