<template>
		<div id="home" class="body1" style="background-color: white;">
			
			<div class="body">
				<nav-header title="信 息" msg="通过连接和聊天来完成任务" btn1="信息" btn2="历史" active="false">
					<span @click="$router.push({name: 'Home'})" class="btn-active">信息</span>
					<span @click="$router.push({name: 'Home_histroy'})">历史</span>
				</nav-header>
				
				<mt-loadmore :top-method="loadTop" ref="loadmore">
				<!-- 消息栏 -->
				<!-- if 无消息 @1-->
				<div id="msgDiv_default" v-if="msgDivDefaultShow">
					<p>“在完成之前似乎总是不可能的”</p>
					<p>-纳尔逊曼德拉</p>
				</div>
				
				<!-- if 有信息 -->
				<ul id="msgDiv" v-if="msgDivShow">
					
					<mt-cell-swipe v-for='(item,index) in msgList' :key="index" title="" :right="[
						{  
				          content: '标记为已读',  
				          style: { background: 'lightgray', color: '#fff' },
				          handler: function() { read(index) }
				        },  
				        {  
				          content: '删除',  
				          style: { background: 'red', color: '#fff' },  
				          handler: function() { del(index) }
				        }]" >
						<router-link :to="{name: 'msg_detial'}">
							<router-link :to="{name: 'OtherInfo'}"><img class="mui-pull-left" src="../assets/img/touxiang1.png" /></router-link>
							<div class="mui-pull-left">
								<h3>{{item.name}}</h3>
								{{item.index}}
								<p class="mui-ellipsis">{{item.content}}</p>
							</div>
							<span class="msgTime mui-pull-right">{{item.datetime}}</span>
							<div v-if="item.new" class="littleBall active"></div>
							<div v-if="!item.new" class="littleBall"></div>
						</router-link>
					</mt-cell-swipe>
				</ul>
				</mt-loadmore>
				
			</div>
			
			<nav-footer active="info"></nav-footer>
			
		</div>
	
</template>

<script>
import NavHeader from '../components/NavHeader1.vue';
import NavFooter from '../components/NavFooter.vue';
	export default {
		data() {
			return {
				msgDivDefaultShow: false,	//是否显示默认的信息栏
				msgDivShow: true,			//是否显示有信息的信息栏
				msgList: [					// 信息列表
					{name: 'Allen', content: '好久不见了，你家的猫咋样？', datetime: "14:25", new: true, query: ''},
					{name: 'Bell', content: '你最近在干嘛啊？', datetime: "14:25", new: true, query: ''},
					{name: 'Worker', content: '我最近发现了一个新餐厅，走起！', datetime: "14:25", new: false, query: ''},
					{name: 'Nazi', content: '喂！你的团被人灭了！', datetime: "14:25", new: false, query: ''}
				],
			}
		},
		components: {
			NavHeader,
			NavFooter
		},
		mounted() {
			$(".mint-loadmore").css('height', window.innerHeight);
			this.init();
		},
		methods: {
			init() {
				// 判断是否没有信息
				if(this.msgList.length == 0) {
					this.msgDivDefaultShow = true;
					this.msgDivShow = false;
				}
			},
			loadTop() {
				// 加载数据
//			  	this.allLoaded = true;// 若数据已全部获取完毕
//			  	this.$refs.loadmore.onBottomLoaded();
				location.reload();
			},
        	del(id) {
        		this.msgList.splice(id, 1);
        		if(this.msgList.length == 0) {
        			this.msgDivDefaultShow = true;
        		}
        	},
        	read(id) {
        		this.msgList[id].new = false;
        	}
		}
	}
</script>

<style scoped="scoped">
#msgDiv .mint-cell-value a {
	width: 100%;
}
#msgDiv {
    margin-top: 20px;
}
#msgDiv img {
    width: 38px;
    height: 38px;
    background-color: paleturquoise;
    margin-right: 10px;	
    border-radius: 5px;
}
#msgDiv .mui-pull-left h3 {
    font-size: 15px;
    color: #9d9d9d;
    padding-bottom: 8px;
}
#msgDiv .mui-pull-left p {
	font-size: 13px;
	width: 140px;
	color: black;
}
.msgTime {
	font-size: 14px;
    color: #9d9d9d;
    padding-top: 10px;
    position: absolute;
    right: 4%;
}
.littleBall {
	position: absolute;
    width: 8px;
    height: 8px;
    background-color: #535353;
    border-radius: 50%;
	top: -3px;
	left: -3px;
    border: 1px solid white;
}
.littleBall.active {
	background-color: #4fb55d;
}
#msgDiv_default {
	margin-top: 60px;
}
#msgDiv_default p {
	font-size: 18px;
    text-align: center;
    margin-top: 20px;
    color: #9d9d9d;
}

</style>


