<template>
	<div>
		<a href="javascript:;" @click="show">
			<slot></slot>
		</a>
	</div>
</template>

<script>
import { MessageBox } from 'mint-ui';
	export default {
		props: ['title', 'confirmButtonText', 'cancelButtonText'],
		data() {
			return {
			}
		},
		methods: {
			show() {
				var htmls =  '<div style="width:100%; height:100%;overflow:hidden;">'+
								'<h1 style="width:100%;height: 40px;position: absolute;left:0; top:0;color:black;"><span>提示</span></h1>'+
				                '<h1 style="font-size:18px;padding-top:40px">'+ this.title +'</h1>'+
			            	"</div>";
				MessageBox.confirm('', {
					title: "",
				  	message: htmls,
				  	confirmButtonText: this.confirmButtonText,
				  	cancelButtonText: this.cancelButtonText
				}).then(action => {
					if(action == 'confirm') {
						this.$emit('confirm');
					}
				}).catch(err => {
					return;
				})
			}
				
		}
	}
</script>

<style>
</style>


