function sendMsg1 (event, arr) {
	// 发送消息-按下enter键 发送
	var msg = event.target.innerText;
	msg = msg.replace(/[\r\n]/g,"")		// 去除换行符
	if(msg == "") {
		event.target.innerText = "";
		return;
	}
	event.target.innerText = "";
	var msgObj = {
		name: '回答者',
		floor: arr.length+1,
		datetime: "6-21 13 : 00",
		img_src: '/static/img/touxiang2.d4dd0a8.jpg',
		dianzan: 0,
		dianzan_num: 1,		// 给某人允许点赞的次数，一定要让后端加上！
		huifu_num: 0,		// 已经回复的信息数
		comment: msg
	}
	return msgObj;
}
function sendMsg2 (event, arr) {
	// 发送消息-点击发送按钮 发送
	var msg = $('#msg').html();
	if(msg == "" || msg == "<div><br></div><div><br></div>" || msg == "<div><br></div>") {
		$('#msg').html("");
		return;
	}
	var msgObj = {
		name: '回答者',
		floor: arr.length+1,
		datetime: "6-21 13 : 00",
		img_src: '/static/img/touxiang2.d4dd0a8.jpg',
		dianzan: 0,
		dianzan_num: 1,		// 给某人允许点赞的次数，一定要让后端加上！
		huifu_num: 0,		// 已经回复的信息数
		comment: msg
	}
//	this.answerList.push(msgObj);d
	$('#msg').html('');
	$('#msg').focus();
	return msgObj;
}

export {sendMsg1, sendMsg2};



