<template>
	<div>
		<a href="javascript:;" @click="show">
			<slot></slot>
		</a>
		<div v-if="showBox" id="btn" @click="toBidQuestion"></div>
	</div>
</template>

<script>
import { MessageBox } from 'mint-ui';
	export default {
		data () {
			return {
				showBox: false,		// div是否出现
			}
		},
		mounted() {
			
		},
		methods: {
			toBidQuestion() {
				$('.mint-msgbox').hide();
				$('.v-modal').hide();
				this.$router.push({name: 'bid_question'});
			},
			show() {
				this.showBox = true;
				var htmls =  '<div style="width:100%; height:100%;overflow:hidden;">'+
								'<h1 style="width:100%;height: 66px;background-color: #cc3232;position: absolute;left:0; top:0;"><img style="width:45px;margin-top:10px;" src="data:image/png;base64,"/></h1>'+
				                '<h1 style="color:#cc3232; font-size:20px;margin-top:80px;">投标失败</h1>'+
				                '<p style="font-size:0.9rem; color: #7d7d7d;font-family:'+ '黑体' +';padding-bottom: 6px;">所有款项待会会自动退还到原账户</p>'+
				                '<p class="mui-clearfix"><span style="font-size:0.9rem;color:#0087e0;float:left;padding-left: 20px;">投标常见问题</span>'+
				                '<img style="float:right; width:10px;margin-top: 13px;margin-right: 20px;" src="data:image/png;base64,"/></p>'+
			            	"</div>";
				MessageBox.alert('', {
					title: '',
				  	message: htmls,
				  	confirmButtonText: '好的'
				}).then(action => {
				 	this.showBox = false;
				});
			}
				
		}
	}
</script>

<style>
#btn {
    width: 80%;
    height: 40px;
    background: transparent;
    position: fixed;
    left: 10%;
    z-index: 100000;
    top: 56.8%;
}
</style>


