<template>
	<div id="message" style="background-color: white;">
		<!-- header -->
		<header class="mint-header">
			<div class="mint-header-button is-left">
				<a href="javascript:;" @click="$router.go(-1)" class="router-link-active"><button class="mint-button mint-button--default mint-button--normal"><span class="mint-button-icon"><i class="mintui mintui-back"></i></span> <label class="mint-button-text">返回</label></button></a>
			</div>
			<h1 class="mint-header-title">
				张彬
				<h3>在线</h3>
			</h1>

			<div class="mint-header-button is-right"><button  @click="showAction" class="mint-button mint-button--default mint-button--normal"><span class="mint-button-icon"><i class="mintui mintui-more"></i></span> <label class="mint-button-text"></label></button></div>
		</header>
		
		<!--<mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">-->
		<div class="container mui-clearfix" id="container" v-chat-scroll @click="bottom">
			<!-- 别人的发言 -->
			<li class="other mui-clearfix">
				<div class="mui-pull-left">
					<h5>张彬 <small>软件开发者</small></h5>
					<img src="../../assets/img/touxiang1.png" class="mui-pull-left" />
					<span class="msg mui-pull-left">
						<i class="tri"></i>
						fdsafdsfsfdsafssfdfsfafdsfdsaf
					</span>
				</div>
			</li>

			<!-- 我的发言 -->
			<li class="mine mui-clearfix">
				<div class="mui-pull-right">
					<h5><i></i></h5>
					<div class="msg mui-pull-left">
						<i class="tri"></i>
						fdsafdsfsfdsafssfdfsfafdsfdsaffdsfsfdsafssfdfsfafdsfdsafdsfs
					</div>
				</div>
			</li>
			
			<!-- 别人的发言 -->
			<li class="other mui-clearfix">
				<div class="mui-pull-left">
					<h5>张彬 <small>软件开发者</small></h5>
					<img src="../../assets/img/touxiang1.png" class="mui-pull-left" />
					<span class="msg mui-pull-left">
						<i class="tri"></i>
						fdsafdsfsfdsafssfdfsfafdsfdsaffdsfdsfsfdsfdsfdsfdsfdsfsafdsfsdafsdfdsfdsafsdafdsfsfsdafdsafdsafdsfnjdskaahfdsgk akfafksdf
					</span>
				</div>
			</li>
			
			<!-- 我的发言 -->
			<li class="mine mui-clearfix">
				<div class="mui-pull-right">
					<h5><i></i></h5>
					<div class="msg mui-pull-left">
						<i class="tri"></i>
						fdsafdsfsfdsafssfdfsfafdsfdsaffdsfsfdsafssfdfsfafdsfdsafdsfs
					</div>
				</div>
			</li>
			
			<!-- 别人的发言 -->
			<li class="other mui-clearfix">
				<div class="mui-pull-left">
					<h5>张彬 <small>软件开发者</small></h5>
					<img src="../../assets/img/touxiang1.png" class="mui-pull-left" />
					<span class="msg mui-pull-left">
						<i class="tri"></i>
						fdsafdsfsfdsafssfdfsfafdsfdsaffdsfdsfsfdsfdsfdsfdsfdsfsafdsfsdafsdfdsfdsafsdafdsfsfsdafdsafdsafdsfnjdskaahfdsgk akfafksdf
					</span>
				</div>
			</li>
			
			<!-- 我的发言 -->
			<li class="mine mui-clearfix" v-for="(item, index) in msg" :key="index">
				<div class="mui-pull-right">
					<h5><i></i></h5>
					<div class="msg mui-pull-left">
						<i class="tri"></i>
						{{item.content}}
					</div>
				</div>
			</li>
			
		</div>
		<!--</mt-loadmore>-->
		<footer>
			<div class="msgbox">
				<img class="mui-pull-left" src="../../assets/img/icon-actionSheet.png" @click="showAction" />
				<p contenteditable="true" id="msg" type="text" name="msg" @keyup.prevent.enter="sendMsg1" @focusin="top"></p>
				<img @click="sendMsg2" class="mui-pull-right" src="../../assets/img/icon-send.png" />
			</div>
			<div style="height: 35px;z-index: 99;"></div>
		</footer>

		<!-- actionSheet-->
		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
	</div>
</template>

<script>
import '@/assets/css/sendMsg.css'
	export default {
		data() {
			return {
				actions: [
					{ "name": "相机" },
					{ "name": "相册" },
					{ "name": "文件" }
				],
				sheetVisible: false,
				msg: [
					{name:'mine', content: '我是谁?我在哪里?我要往哪里去？'},
					{name:'mine', content: '我是谁?我在哪里?我要往哪里去？'}
				]
			}
		},
		mounted() {
//			this.initHeight();
			
//			this.sendMsg1();
		},
		methods: {
			loadTop () {
				// 加载更多数据
//				this.allLoaded = true;// 若数据已全部获取完毕
//				this.$refs.loadmore.onBottomLoaded();
				window.Location.reload();
			},
			showAction() {
				this.sheetVisible = true;
			},
			sendMsg1(event) {
				// 发送消息-按下enter键 发送
				var msg = event.target.innerText;
				msg = msg.replace(/[\r\n]/g,"")		// 去除换行符
				if(msg == "") {
					event.target.innerText = "";
					return;
				}
				event.target.innerText = "";
				var msgObj = {
					name: 'mine',
					content: msg
				}
				this.msg.push(msgObj);
				$("#msg").focus();
			},
			sendMsg2() {
				// 发送消息-点击发送按钮 发送
				var msg = $('#msg').html();
				if(msg == "" || msg == "<div><br></div><div><br></div>" || msg == "<div><br></div>") {
					$('#msg').html("");
					return;
				}
				$('#msg').html("");
				var msgObj = {
					name: 'mine',
					content: msg
				}
				this.msg.push(msgObj);
				$("#msg").focus();
			},
			top() {
				$('.msgbox').css('bottom', '35px');
				$('.container').css('bottom', '90px');
			},
			bottom() {
				$('.msgbox').css('bottom', '0px');
				$('.container').css('bottom', '50px');
			}
		}
	}
</script>



