// 1.滚动条从顶部往下滑的时候， .boxBar固定在顶部
$(window).scroll(function() {
	if($(window).scrollTop() >= 290) {
//		alert("html: " + $(window).scrollTop())
		$('#otherInfo .boxBar').css({
			'position': 'fixed',
			'top': '40px',
			'z-index': 80
		});
		$('#otherInfo .container').css({
			"padding-bottom": '50px',
			"padding-top": "50px"
		})
	}
	if($(window).scrollTop() <= 290) {
//		alert("html: " + $(window).scrollTop())
		$('#otherInfo .boxBar').css({
			'position': 'static',
			'top': '0'
		});
		$('#otherInfo .container').css({
			"padding-bottom": '0',
			"padding-top": "10px"
		})
		$('#otherInfoQuestion').css({
			"padding-bottom": '50px',
			"padding-top": "0"
		})
	}
});








