<template>
	<div id="ProjectDescription" style="background-color: white;">
		<nav-header title="项目说明" next="true">
	        <span v-if="next" @click="next">Next</span>
		</nav-header>
		<hr class="hr1" />
		
		<div class="container mui-clearfix">
			<p class="title">项目描述</p>
			<p contenteditable="true" @click="check" @focusout="focusOut" class="neirong">
				<span v-if="description">在这里描述您的项目</span>
			</p>
			<!--<textarea autoHeight="true" class="neirong" placeholder="在这里描述您的项目"></textarea>-->
			<hr class="hr2" />
		</div>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader4.vue'
	export default {
		data() {
			return {
				description: true,
			}
		},
		components: {
			NavHeader
		},
		mounted() {
			// textarea高度自适应
//			$.fn.autoHeight = function(){    
//		        function autoHeight(elem){
////		            elem.style.height = 'auto';
////		            elem.scrollTop = 0; //防抖动
////		            elem.style.height = elem.scrollHeight + 'px';
////		            elem.scrollTop = elem.style.height;
//		        }
//		        this.each(function(){
//		            autoHeight(this);
//		            $(this).on('keyup', function(){
//		                autoHeight(this);
//		            });
//		        });     
//		    }                
//		    $('textarea[autoHeight]').autoHeight();    
		    
		   
		},
		methods: {
			next() {
				this.$router.push({name: 'projectInfo'});
			},
			check(event) {
				var value = event.target.innerText;
				var isTrue = true;
				if(value != "") {
					isTrue = false;
				}else {
					isTrue = true;
				}
				this.disabled = isTrue;
				this.description = isTrue;
				
			},
			toProjectDescription() {
				this.$router.push({name: 'projectDescription'});
			},
			focusOut() {
				var value = event.target.innerText;
				var isTrue = true;
				if(value != "") {
					isTrue = false;
				}else {
					isTrue = true;
				}
				console.log(isTrue);
				this.description = isTrue;
			}
		}
	}
</script>

<style scoped="scoped">
.hr1 {
	position: fixed;
    width: 75%;
    height: 2px;
    border: 0;
    background: #009cdd;
    top: 32px;
}
.container {
	padding: 80px 25px 0;
}
.container .title {
	font-size: 14px;
    font-weight: bold;
}
.container p:focus {
	outline: 0;
}
textarea:focus { outline: none;}
.neirong {
    padding: 20px 10px 0 0;
	line-height: 20px;
    font-size: 14px;
    word-break: break-all;
    border: 0; 
    width: 100%; 
    resize: none;
    font-family: "黑体";
}
.hr2 {
	width: 100%;
	height: 2px;
	background-color: #0385e3;
	border: 0;
}
.neirong span {
	color: #959393;;
}
</style>


