<template>
	<div id="ProjectRequire" style="background-color: white;">
		<nav-header title="发布项目"></nav-header>
		<hr />
		
		<div class="container">
			<h1>你需要一个新的网站还是一个旧的网站</h1>
			<ul>
				<li class="mui-clearfix" @click="toProjectBudget">
					<p class="mui-pull-left">我需要一个新的网站</p>
					<img src="../../assets/img/icon-right3.png" class="icon-right" />
				</li>
				<li class="mui-clearfix" @click="toProjectBudget">
					<p class="mui-pull-left">我需要一个旧的网站</p>
					<img src="../../assets/img/icon-right3.png" class="icon-right" />
				</li>
			</ul>
		</div>
			
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader4.vue'
	export default {
		data() {
			return {}
		},
		components: {
			NavHeader
		},
		methods: {
			toProjectBudget() {
				this.$router.push({name: 'projectBudget'});
			},
		}
	}
</script>

<style scoped="scoped">
hr {
	position: fixed;
    width: 25%;
    height: 2px;
    border: 0;
    background: #009cdd;
    top: 32px;
}
.container {
	padding: 50px 25px;
}
.container h1 {
    font-size: 24px;
    font-weight: bold;
    padding-top: 45px;
    line-height: 40px;
	padding-bottom: 20px;
    border-bottom: 1px solid #efefef;
}
.container li {
	padding: 21px 0;
    border-bottom: 1px solid #efefef;
}
li p {
    font-size: 14px;
}
</style>


