<template>
	<header id="NavHeader5">
		<img @click="$router.go(-1)" src="../assets/img/icon-left2.png" class="mui-pull-left"/>
		<span class="mui-pull-left" v-text="text"></span>
		<span class="mui-pull-right" v-text="text2" @click="goback"></span>
	</header>
</template>

<script>
	export default {
		props: ['text', 'text2', 'pathname'],
		data() {
			return {}
		},
		methods: {
			goback() {
				var p = this.pathname;
				this.$router.push({name: p});
			}
		}
	}
</script>

<style scoped="scoped">
header {
	width: 100%;
	height: 40px;
	padding: 0 10px;
	background-color: #0087e0;
	line-height: 40px;
	box-sizing: border-box;
}
header img {
	width: 10px;
    margin-top: 11px;
}
header span {
	font-size: 20px;
	font-weight: bold;
	color: white;
	margin-left: 7px;
}
header span.mui-pull-right {
	font-size: 12px;
	font-family: "黑体";
	font-weight: normal;
}
</style>


