<template>
	<div id="Recharge" style="background-color: white;">
		<header>
			<span>提现</span>
			<span class="mui-pull-right" @click="$router.go(-1)">×</span>
		</header>
		
		<div class="container">
			<p style="color: #c1c2c4; font-size: 12px; padding-bottom: 10px;">可用余额</p>
			<div class="priceDiv">
				<span>￥2.22</span>
				<span class="mui-pull-right">CNY <img width="8" src="../../assets/img/icon-right3.png"/></span>
			</div>
			
			<span class="title">选择提现方法</span>
			<div class="btn-group">
				<span @click="active=true" :class="{'active': active}">微信</span>
				<span @click="active=false" :class="{'active': !active}">支付宝</span>
			</div>
			
			<ul>
				<li>
					<input type="number" placeholder="输入金额" @keyup.enter="input" @keyup.delete="del" autofocus="autofocus" />
					<span class="mui-pull-right coin" @click="show">{{coin}} <img width="13" src="../../assets/img/icon-down2.png" /></span>
					<div class="chooseDiv">
						<span class="choose" @click="choose">人民币</span>
						<span @click="choose">港币</span>
						<span @click="choose">美元</span>
						<span @click="choose">日元</span>
					</div>
				</li>
				<li>
					<input type="text" placeholder="姓名" @keyup.enter="input" @keyup.delete="del"/>
				</li>
				<li>
					<input type="number" placeholder="账号" @keyup.enter="input" @keyup.delete="del" />
				</li>
			</ul>
			
			<span class="title">提现明细</span>
			<div class="paymentDetail">
				<p>
					<span>提现金额</span>
					<span class="mui-pull-right">￥2.22  CNY</span>
				</p>
				<p>
					<span>手续费</span>
					<span class="mui-pull-right">￥2.22  CNY</span>
				</p>
				<hr />
				<p class="total">
					<span>合计</span>
					<span class="mui-pull-right">￥4.44  CNY</span>
				</p>
			</div>
			
			<footer>
				<a :class="{'btn-active': false}" href="javascript:;">提现 ￥0.00 CNY</a>
			</footer>
		</div>
	</div>
</template>

<script>
import {checkInput} from '@/assets/js/Recharge.js'
	export default {
		data() {
			return {
				active: true,		// 指定.btn-group中的被选中的span
				coin: "人民币"
			}
		},
		mounted() {
			// 进入该页面的时候，滚动条在顶部
			$(window).scrollTop(0);
			checkInput();
		},
		computed: {
			
		},
		methods: {
			input(event) {
				$(event.target).parent().next().children('input').focus();
			},
			del(event) {
				// 功能：一直按着delete键向上删除
				if(event.target.value == "") {
					$(event.target).parent().prev().children("input").focus();
				}
			},
			show(event) {
				$(event.target).next().slideToggle('fast');
			},
			choose(event) {
				$(event.target).addClass('choose').siblings().removeClass('choose');
				this.coin = event.target.innerText;
				console.log(event.target.innerText);
				$('.chooseDiv').slideUp('fast');
			},
			
		}
	}
</script>

<style scoped="scoped">
header {
	padding: 20px 25px;
}
header span:first-child {
	font-size: 24px;
    padding-bottom: 16px;
    border-bottom: 2px solid;
    font-weight: bold;
}
header span:last-child {
	color: #0889dc;
	font-size: 40px;
    margin-top: -11px;
}
.container {
	padding: 20px 25px;
}
.priceDiv {
	width: 100%;
	height: 66px;
	line-height: 66px;
	background-color: #f1faff;
	color: #0689d7;
	font-size: 24px;
	padding: 0 10px;
	box-sizing: border-box;
    margin-bottom: 27px;
}
.priceDiv span:last-child {
	font-size: 15px;
}
.title {
	font-size: 15px;
	font-weight: bold;
	padding-bottom: 8px;
	border-bottom: 2px solid;
}
.btn-group {
	width: 100%;
	height: 72px;
	background-color: #f0f0f0;
	border-radius: 5px;
	margin-top: 27px;
	padding: 6px;
	box-sizing: border-box;
	margin-bottom: 29px;
}
.btn-group span {
	display: inline-block;
	width: 50%;
	height: 100%;
	text-align: center;
	float: left;
	color: #75787d;
	line-height: 57px;
	font-weight: bold;
	font-size: 15px;
}
.btn-group .active {
	background-color: white;
	color: black;
	box-shadow: 1px 2px 3px rgba(0,0,0,0.15);
    border-radius: 5px;
}
ul {
	margin-bottom: 50px;
}
li {
    padding-top: 30px;
    border-bottom: 1px solid #c1c1c1;
    padding-bottom: 5px;
    position: relative;
}
li input {
	border: 0;
    font-size: 15px;
    width: 73%;
}
li span.mui-pull-right {
	font-size: 13px;
    color: #0689d7;
    font-weight: bold;
}
input::-webkit-input-placeholder { /* WebKit browsers */ 
	color: #bfbfc1; 
} 
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
	color: #bfbfc1; 
} 
input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
	color: #bfbfc1; 
} 
input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
	color: #bfbfc1; 
} 
.paymentDetail {
	width: 100%;
	height: 140px;
	background-color: #f7f7f7;
    margin-top: 30px;
    padding: 21px 15px;
    box-sizing: border-box;
}
.paymentDetail p {
    font-size: 12px;
    font-family: "黑体";
}
.paymentDetail p:first-child {
    margin-bottom: 26px;	
}
hr {
	border: 0;
	height: 1px;
	background-color: black;
}
.paymentDetail .total {
	font-size: 15px;
	font-weight: bold;
	padding: 5px;
}
footer a {
	display: block;
	width: 100%;
	height: 56px;
	background-color: #b2dcf5;
	border-radius: 5px;
	color: white;
	text-align: center;
	line-height: 56px;
	margin-top: 20px;
    font-size: 15px;
    font-weight: 500;
}
footer a.btn-active {
	background-color: #0689d7;
}
.coin {
	padding: 6px 3px 6px 10px;
    box-sizing: border-box;
    border: 1px solid #e1e1e1;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    border-bottom: 0;
    margin-top: 0;
    width: 71px;
    text-align: right;
}
.chooseDiv {
    float: right;
    position: absolute;
    top: 56px;
    right: 0;
    border: 1px solid #e1e1e1;
    width: 70px;
    background-color: white;
    display: none;
    z-index: 100;
    text-align: right;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top: 0;
}
.chooseDiv span{
	display: block;
    padding: 4px 16px 4px 5px;
    border-bottom: 1px solid #eee;
    color: #999;
    font-weight: normal;
    font-size: 14px;
}
.chooseDiv span:last-child {
	border-bottom: 0;
}
.chooseDiv span.choose {
	border-top: 1px solid #e1e1e1;
	border-radius: 0;
	color: white;
	background-color: #addaf6;
}
</style>


