<template>
	<div id="answerList">
		<h3>回答({{total}})</h3>
		<ul @click="bottom">
			<li class="mui-clearfix" v-for="(item, index) in answerList" :key="index">
				<router-link :to="{name: 'OtherInfo'}"><img class="touxiang mui-pull-left" :src="item.img_src"/></router-link>
				<div class="mui-pull-left">
					<p class="questioner">{{item.name}}</p>
					<p class="mui-pull-left" style="font-size: 13px;margin-left: 9px;padding-top: 5px;color: #aaa;">
						<span class="floor">{{item.floor}} 楼</span>
						<span class="datetime">{{item.datetime}}</span>
					</p>
				</div>
				<p @click="dianzan(item, index)" style="font-size: 16px;top: 0;" class="iconfont icon-dianzan mui-pull-right"> <span style="font-size: 13px;">点赞 {{item.dianzan}}</span></p>
				<div class="neirong">
					{{item.comment}}
				</div>
				
				<div class="footer-icon" style="margin-top: 20px;">
					<p class="mui-pull-left">
						<span class="iconfont icon-pinglun" style="font-size: 12px;float: left;"></span>
						<span @click="$router.push({name: 'response'})" style="font-size: 12px;color: #aaa;float: left;margin-left: 10px;">回复{{item.huifu_num}}</span>
					</p>
					<p class="mui-pull-left" style="margin-left: 10px;">
						<confirm-model title="确定要举报？" cancelButtonText="否" confirmButtonText="是">
							<span class="iconfont icon-jubao" style="font-size: 12px;float: left;"></span>
							<span style="font-size: 12px;color: #aaa;float: left;margin-left: 10px;">举报</span>
						</confirm-model>
					</p>
				</div>
			</li>
		</ul>
		<div class="bottom">没有了</div>
		
		<footer>
			<div class="msgbox">
				<img class="mui-pull-left" src="../../assets/img/icon-actionSheet.png" @click="showAction" />
				<p contenteditable="true" id="msg" type="text" name="msg" @keydown.prevent.enter="sendMsg1" @focusin="top"></p>
				<img @click="sendMsg2" class="mui-pull-right" src="../../assets/img/icon-send.png" />
			</div>
			<div style="height: 35px;z-index: 99;"></div>
		</footer>
		
		<!-- actionSheet-->
		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
	</div>
</template>

<script>
import ConfirmModel from '@/components/Modal/ConfirmModel.vue'
import {sendMsg1, sendMsg2} from '@/assets/js/sendMsg.js'
	export default {
		data() {
			return {
				answerList: [
					{
						name: '回答者3',
						floor: 3,
						datetime: '6-15 19 : 10',
						img_src: '/static/img/touxiang2.d4dd0a8.jpg',
						dianzan: 1,
						dianzan_num: 1,		// 给某人允许点赞的次数，一定要让后端加上！
						huifu_num: 1,		// 已经回复的信息数
						comment: 'Y7000?'
					},
					{
						name: '回答者2',
						floor: 2,
						datetime: '6-15 19 : 30',
						img_src: '/static/img/touxiang2.d4dd0a8.jpg',
						dianzan: 0,
						dianzan_num: 1,		// 给某人允许点赞的次数，一定要让后端加上！
						huifu_num: 2,		// 已经回复的信息数
						comment: '一键还原呗'
					},
					{
						name: '回答者1',
						floor: 1,
						datetime: '6-15 19 : 50',
						img_src: '/static/img/touxiang2.d4dd0a8.jpg',
						dianzan: 0,
						dianzan_num: 1,		// 给某人允许点赞的次数，一定要让后端加上！
						huifu_num: 10,		// 已经回复的信息数
						comment: '我家的电脑也出现了这种情况，咋办啊，才买没几天的，花了我好多钱啊(┬＿┬)'
					}
				],
				actions: [
					{ "name": "相机" },
					{ "name": "相册" },
					{ "name": "文件" }
				],
				sheetVisible: false,
			}
		},
		components: {
			ConfirmModel
		},
		computed: {
			total() {
				return this.answerList.length
			}
		},
		methods: {
			dianzan(item, index) {
				if(item.dianzan_num <=0 ) {
					// 取消点赞
					if ($('.icon-dianzan:eq(' + index + ')').css("color") == 'rgb(255, 165, 0)') {
						item.dianzan--;
						item.dianzan_num++;
						$('.icon-dianzan:eq(' + index + ')').css("color", '#aaa')
					}
					return;
				}else {
					// 点赞
					item.dianzan++;
					item.dianzan_num--;
					$('.icon-dianzan:eq(' + index + ')').css("color", 'orange');
				}
			},
			showAction() {
				this.sheetVisible = true;
			},
			sendMsg1(event) {
				// 发送消息-按下enter键 发送
				var msgObj = sendMsg1(event, this.answerList);
				this.answerList.unshift(msgObj);
			},
			sendMsg2() {
				// 发送消息-点击发送按钮 发送
				var msgObj = sendMsg2(event, this.answerList);
				this.answerList.unshift(msgObj);
			},
			top() {
				$('.msgbox').css('bottom', '35px');
			},
			bottom() {
				$('.msgbox').css('bottom', '0px');
			}
		}
	}
</script>

<style scoped="scoped">
.questioner {
	font-size: 13px;
	color: black;
	font-family: "微软雅黑";
	font-weight: bold;
	margin-bottom: 0;
	padding-top: 5px;
    margin-left: 9px;
}
.questioner small {
	font-size: 9px;
	color: #8d8d8d;
	font-weight: normal;
}
#answerList h3 {
	font-size: 14px;
    font-family: "黑体";
    padding: 10px;
    border-bottom: 1px solid #ccc;
}
#answerList {
    padding-bottom: 50px;
    position: relative;
}
#answerList ul {
	padding: 0 15px 35px;
}
#answerList li {
	padding: 10px 0;
	border-bottom: 1px solid #f0f0f0;
}
#answerList .neirong {
	padding-top: 50px;
    font-size: 13px;
    padding-left: 50px;
    line-height: 16px;
}
.bottom {
    position: absolute;
    bottom: 60px;
    left: 50%;
    margin-left: -20px;
    font-size: 14px;
    color: #999;
}
footer>div {
	width: 100%;
	/*height: 37px;*/
	border-top: 1px solid #eeeeee;
	position: fixed;
	bottom: 0;
	z-index: 100;
    background-color: white;
}
footer img:first-child {
    width: 14px;
    position: absolute;
    bottom: 10px;
    left: 10px;
}
footer img:last-child {
    width: 23px;
    position: absolute;
    bottom: 12px;
    right: 10px;
}
footer #msg {
	float: left;
	width: 77%;
	max-height: 50px;
    font-size: 14px;
    line-height: 20px;
    border: 0;
    border-bottom: 1px solid #dedede;
    vertical-align: super;
    overflow: auto;
    padding: 0 0 5px 8px;
    margin: 13px 0 5px 26px;
    word-break: break-all;
}
footer #msg:focus {
	outline: 0;
}
</style>


