<template>
	<div id="Recharge" style="background-color: white;">
		<header>
			<span>付款</span>
			<span class="mui-pull-right" @click="$router.go(-1)">×</span>
		</header>
		
		<div class="container">
			<p style="color: #c1c2c4; font-size: 12px; padding-bottom: 10px;">应付款</p>
			<div class="priceDiv">
				<span>￥2.22</span>
				<span class="mui-pull-right">CNY <img width="8" src="../assets/img/icon-right3.png"/></span>
			</div>
			
			<span class="title">选择付款方法</span>
			<div class="btn-group">
				<p @click="activeIndex=1" :class="{'active': activeIndex==1}">
					<span>余额</span>
					<span class="mui-pull-right">￥ 2.22</span>
				</p>
				<p @click="activeIndex=2" :class="{'active': activeIndex==2}">
					<span>支付宝</span>
					<span class="mui-pull-right"></span>
				</p>
				<p @click="activeIndex=3" :class="{'active': activeIndex==3}">
					<span>微信</span>
					<span class="mui-pull-right"></span>
				</p>
			</div>
			
			<span class="title">付款明细</span>
			<div class="paymentDetail">
				<p>
					<span>付款金额</span>
					<span class="mui-pull-right">￥2.22  CNY</span>
				</p>
				<p>
					<span>手续费</span>
					<span class="mui-pull-right">￥2.22  CNY</span>
				</p>
				<hr />
				<p class="total">
					<span>合计</span>
					<span class="mui-pull-right">￥4.44  CNY</span>
				</p>
			</div>
			
			<footer class="footer">
				<success-modal title="支付成功">支付 ￥0.00 CNY</success-modal>
				<!--<a href="javascript:;">支付 ￥0.00 CNY</a>-->
			</footer>
		</div>
	</div>
</template>

<script>
import SuccessModal from '@/components/Modal/SuccessModal.vue'
import {checkInput} from '@/assets/js/Recharge.js'
	export default {
		data() {
			return {
				active: false,		// 是否解除支付按钮功能
				activeIndex: 1,		// 指定.btn-group中的被选中的span
			}
		},
		mounted() {
			// 滚动到顶部
			$(window).scrollTop(0);
			
			checkInput()
		},
		components: {
			SuccessModal
		}
	}
</script>

<style scoped="scoped">
header {
	padding: 20px 25px;
}
header span:first-child {
	font-size: 24px;
    padding-bottom: 16px;
    border-bottom: 2px solid;
    font-weight: bold;
}
header span:last-child {
	color: #0889dc;
	font-size: 40px;
    margin-top: -11px;
}
.container {
	padding: 20px 25px;
}
.priceDiv {
	width: 100%;
	height: 66px;
	line-height: 66px;
	background-color: #f1faff;
	color: #0689d7;
	font-size: 24px;
	padding: 0 10px;
	box-sizing: border-box;
    margin-bottom: 27px;
}
.priceDiv span:last-child {
	font-size: 15px;
}
.title {
	font-size: 15px;
	font-weight: bold;
	padding-bottom: 8px;
	border-bottom: 2px solid;
}
.btn-group {
	margin-top: 20px;
}
.btn-group p {
	width: 100%;
	height: 46px;
	padding: 10px 20px;
	background-color: #f0f0f0;
	color: #75787d;
	margin-bottom: 10px;
	border-radius: 5px;
	line-height: 30px;
	font-size: 15px;
	font-family: "黑体";
	box-sizing: border-box;
}
.btn-group p:last-child {
	margin-bottom: 25px;
}
.btn-group p.active {
	background-color: #dbf2ff;
    color: #0689d7;
}
ul {
	margin-bottom: 50px;
}
li {
    padding-top: 30px;
    border-bottom: 1px solid #c1c1c1;
    padding-bottom: 5px;
}
li input {
	border: 0;
    font-size: 15px;
}
li span.mui-pull-right {
	font-size: 13px;
    color: #0689d7;
    font-weight: bold;
}
input::-webkit-input-placeholder { /* WebKit browsers */ 
	color: #bfbfc1; 
} 
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
	color: #bfbfc1; 
} 
input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
	color: #bfbfc1; 
} 
input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
	color: #bfbfc1; 
} 
.paymentDetail {
	width: 100%;
	height: 140px;
	background-color: #f7f7f7;
    margin-top: 30px;
    padding: 21px 15px;
    box-sizing: border-box;
}
.paymentDetail p {
    font-size: 12px;
    font-family: "黑体";
}
.paymentDetail p:first-child {
    margin-bottom: 26px;	
}
hr {
	border: 0;
	height: 1px;
	background-color: black;
}
.paymentDetail .total {
	font-size: 15px;
	font-weight: bold;
	padding: 5px;
}

</style>


