function spread_1 () {
	if($('.zhaiyao').height() <= 105) {
		$('.zhaiyao').animate({height: '+225px'}, 500);		// +225px 表示height的值增加到，225px
		$('.neirong').animate({height: '+158px'}, 500);
		$(".zhaiyao .toggle").css("transform", 'rotate(180deg)');
	}else if($('.zhaiyao').height() >= 225) {
		$('.zhaiyao').animate({height: '105px'}, 500);
		$('.neirong').animate({height: '25px'}, 500);
		$(".zhaiyao .toggle").css("transform", 'rotate(360deg)');		//与原来的样式来比较，比如原来的为0deg
	};
}

function spread_2 () {
	if ($('.skillDiv').height() <= 180) {
		$('.skillDiv').animate({height: '340px'}, 500);
		$(".skillDiv .toggle").css("transform", 'rotate(180deg)');
	}else if($('.skillDiv').height() >= 340) {
		$('.skillDiv').animate({height: '180px'}, 500);
		$(".skillDiv .toggle").css("transform", 'rotate(360deg)');
	}
}

function spread_3 () {
	if ($('.test').height() <= 217) {
		$('.test').animate({height: '345px'}, 500);
		$(".test .toggle").css("transform", 'rotate(180deg)');
	}else if($('.test').height() >= 345) {
		$('.test').animate({height: '217px'}, 500);
		$(".test .toggle").css("transform", 'rotate(360deg)');
	}
}

function spread (h) {
	if ($('.neirong').height() <= 100) {
		$('.neirong').animate({height: h}, 500);
		$(".neirong .toggle").css("transform", 'rotate(180deg)');
	}else if($('.neirong').height() >= h) {
		$('.neirong').animate({height: "100px"}, 500);
		$(".neirong .toggle").css("transform", 'rotate(360deg)');
	}
}

function spread_auto (h, i, initHeight) {
	if ($('li:eq('+i+')').height() <= initHeight) {
		$('li:eq('+i+')').animate({height: h}, 500);
		$(".icon-more:eq("+i+")").css("transform", 'rotate(180deg)');
	}else if($('li:eq('+i+')').height() >= h) {
		$('li:eq('+i+')').animate({height: initHeight+"px"}, 500);
		$(".icon-more:eq("+i+")").css("transform", 'rotate(360deg)');
	}
}


export {spread_1, spread_2, spread_3,spread, spread_auto};



