<template>
	<div id="otherInfo" style="background-color: white;">
		<nav-header title="张彬" ></nav-header>
		
		<mt-loadmore :top-method="loadTop" ref="loadmore">
			<div>
				<div class="info">
					<img class="touxiang" src="../../assets/img/touxiang2.jpg" />
					<p>张彬</p>
					<div class="level">
						<img src="../../assets/img/start.png" />
						<img src="../../assets/img/start.png" />
						<img src="../../assets/img/start.png" />
						<img src="../../assets/img/start.png" />
						<img src="../../assets/img/start.png" />
						<span>5.0(824)</span>
						<p><img src="../../assets/img/icon-position.jpg"/> 绵阳，四川</p>
					</div>
					<div class="infoDetail mui-clearfix">
						<div class="mui-pull-left">
							<p>完成</p>
							<p>99%</p>
						</div>
						<div class="mui-pull-left">
							<p>预算</p>
							<p>99%</p>
						</div>
						<div class="mui-pull-left">
							<p>准时</p>
							<p>99%</p>
						</div>
					</div>
				</div>
				
				
				<!-- 子组件 -->
				<router-view :active="active" v-on:toPublish="toPublish" v-on:toBought="toBought"></router-view>
			</div>
		</mt-loadmore>
		
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader3.vue';
//import {$} from '@/assets/js/OtherInfo.js';
import {scroll} from '@/assets/js/OtherInfo.js';
	export default {
		beforeRouteEnter (to, from, next) {
			if(from.name == 'answer') {
				next(vm => {
					vm.active = 'tab-container3'
				})
			}else if(from.name == 'msg_detial') {
				next(vm => {
					vm.active = 'tab-container4'
				})
			}else {
				next();
			}
		},
		data() {
			return {
				active: 'tab-container1',
			}
		},
		components: {
			NavHeader
		},
		mounted() {
		},
		methods: {
			loadTop() {
				location.reload(); 
			},
			allLoaded(){},
			loadBottom(){},
			toPublish() {
				this.active = 'tab-container2';
			},
			toBought() {
				this.active = 'tab-container2';
			}
		}
	}
</script>

<style scoped="scoped">
.info {
	padding: 65px 0 0;
	text-align: center;
	background-color: #b2b1ad;
}
.info .touxiang {
	width: 130px;
	height: 130px;
	background-color: palegoldenrod;
}
img.touxiang {
	display: block;
	width: 130px;
	margin: 0 auto;
}
.info p {
	line-height: 34px;
	color: white;
}
.level img {
	width: 17px;
}
.level span {
	background-color: transparent;
}
.level p img{
	width: 9px;
}
.level p {
	font-size: 9px;
}
.infoDetail div{
	width: 32%;
	border-right: 2px solid #adaca9;
}
.infoDetail div:last-child {
	border: 0;
}
.infoDetail div p:first-child {
	font-size: 13px;
	color: #9a9a9a;
	line-height: 14px;
}
.box {
	/*padding-top: 330px;*/
	margin-bottom: 10px;
}
.boxBar {
	width: 100%;
    /*position: fixed;*/
	border-bottom: 1px solid #f0f0f0;
    background-color: white; 
    padding-top: 15px;
    z-index: 100;
}
.boxBar li {
    float: left;
    width: 15%;
    text-align: center;
    font-size: 10px;
    font-weight: bold;
    margin: 0 5%;
    padding-bottom: 15px;
}
.boxBar li.active {
	border-bottom: 2px solid #0087e0;
}
.boxBar li a {
	text-decoration: none;
	color: black;
}
</style>


