<template>
	<div>
		<a href="javascript:;" @click="show">
			<slot></slot>
		</a>
	</div>
</template>

<script>
import { MessageBox } from 'mint-ui';
	export default {
		props: ['title'],
		data () {
			return {
				showModal: false,		// 模态框是否出现
			}
		},
		mounted() {
			
		},
		methods: {
			
			show() {
				this.showModal = true;
				var htmls =  '<div style="width:100%; height:100%;overflow:hidden;">'+
								'<h1 style="width:100%;height: 66px;background-color: #0087e0;position: absolute;left:0; top:0;"><span class="iconfont icon-duihaocheckmark17"></span></h1>'+
				                '<h1 style="color:#0087e0; font-size:20px;margin-top:80px;">'+ this.title +'</h1>'+
			            	"</div>";
				MessageBox.alert('', {
					title: '',
				  	message: htmls,
				  	confirmButtonText: '好的'
				}).then(action => {
				 	this.showModal = false;
				 	this.$emit('todo');	//调用父组件的todo方法
				});
			}
				
		}
	}
</script>

<style>
#btn {
    width: 80%;
    height: 40px;
    background: transparent;
    position: fixed;
    left: 10%;
    z-index: 100000;
    top: 55.8%;
}
</style>


