<template>
	<div style="position: relative;background-color: white;">
		<nav-header text="注册"></nav-header>
		
		<div class="container">
			<input type="email" placeholder="邮箱" name="email" @keyup.enter="enter" @keyup.delete="del" />
			<input type="text" placeholder="用户名" name="username" @keyup.enter="enter" @keyup.delete="del" />
			<input type="password" placeholder="密码" name="pwd" @keyup.enter="enter" @keyup.delete="del" />
			<footer class="btn-regist">
				<success-modal title="注册成功" v-on:todo="todo()">注册</success-modal>
			</footer>
		</div>
		
		<p class="footer">注册表示我同意服务条款</p>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader5.vue'
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {}
		},
		components: {
			NavHeader,
			SuccessModal
		},
		mounted() {
			$('.container').css('height', (window.innerHeight-40));
		},
		methods: {
			todo() {
				this.$router.push({name: 'Home'})
			},
			enter(event) {
				$(event.target).next().focus();
			},
			del(event) {
				$(event.target).prev().focus();
			}
		}
	}
</script>

<style scoped="scoped">
.container {
	padding: 115px 30px 0;
	box-sizing: border-box;
	color: white;
	background-color: #0087e0;
    font-family: "黑体";
}
p.footer {
	width: 100%;
	font-size: 12px;
	text-align: center;
	position: absolute;
	bottom: 10px;
	color: white;
}
.container input {
	width: 100%;
    border: 0;
    background-color: transparent;
    color: white;
    padding-bottom: 17px;
    margin-bottom: 38px;
    border-bottom: 1px solid white;
    font-size: 14px;
}
input::-webkit-input-placeholder { /* WebKit browsers */ 
	color: white; 
} 
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
	color: white; 
} 
input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
	color: white; 
} 
input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
	color: white; 
}

</style>


