<template>
	<div>
		<mt-header :title="title" class="navHeader3"> 
		  <a href="javascript:;" slot="left" @click="goback">
		    <mt-button icon="back">返回</mt-button>
		  </a>
		</mt-header>
	</div>
</template>

<script>
	export default {
		props: ['title', 'pathname'],
		data() {
			return {}
		},
		methods: {
			goback() {
				var p = this.pathname;
				this.$router.push({name: p});
			}
		}
	}
</script>



