<template>
	<div style="background-color: white;">
		<nav-header title="问答"></nav-header>
			<div class="container">
				<h3>问题描述</h3>
				<p contenteditable="true" name="question" ref='msg' @focusin="placeholder=false">
					<span class="placeholder" v-if="placeholder">请认真描述你所遇到的问题</span>
				</p>
				
				<hr />
				<button class="price">
					<span>￥</span>
					<input type="number" value="2.00" />
					<span>人民币</span>
				</button>
				<p class="description">赏金根据自己的意愿来设置，也可以设置0赏金，有人答就会免费公开。</p>
				
				<footer class="footer-btn">
					<success-modal title="提交问题成功">提交问题</success-modal>
				</footer>
			</div>

	</div>
		
</template>

<script>
import NavHeader from '@/components/NavHeader3.vue'
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {
				placeholder: true,
			}
		},
		components: {
			NavHeader,
			SuccessModal
		},
		methods: {
			
		}
	}
</script>

<style scoped="scoped">
.container {
	padding: 70px 10px;
}
h3 {
	padding-bottom: 30px;
}
p {
    width: 100%;
    border: 0;
    height: auto;
    overflow: visible;
    word-break: break-all;
    font-size: 12px;
    line-height: 16px;
    padding: 10px 2px;
}
p:focus{
	outline: none;
}
hr {
	height: 1px;
    border: 0px;
    background-color: #dedede;
	margin-bottom: 30px;
}
.price {
	font-size: 14px;
	color: #0087e0;
	background-color: #f5f9fd;
	border: 0;
	width: 100%;
	padding: 20px 15px;
}
.price span:first-child {
	float: left;
}
.price span:last-child {
	float: right;
}
.price input {
	border: 0;
    background-color: transparent;
    color: #0087e0;
    font-size: 14px;
    float: left;
    padding: 0;
    margin-top: -2px;
}
.description,
.placeholder {
	color: #b4b4b4;
}
</style>


