<template>
	<div class="body1" style="background-color: white;">
			<div class="body">
				<nav-header title="我的项目" msg="可以在这里管理你的项目">
					<span @click="$router.push({name: 'MyProject'})"  class="btn-active">进行中项目</span>
					<span @click="$router.push({name: 'MyProject_Complete'})">已完成项目</span>
				</nav-header>
				
				<mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
					<ul id="projectList">
						<li class="mui-clearfix" @click="$router.push({name: 'projectManager'})">
							<div class="mui-pull-left">
								<h3>软件开发</h3>
								<p class="mui-ellipsis">软件开发是根据用户要求建造出软件系统或者系统中的软件部分的过程。</p>
								<p>
									<span class="mui-pull-left">-- 18/05/04</span>
									<span class="mui-pull-right">14:15:15</span>
								</p>
								<p>￥1,066.64 人民币·还剩6天·3天</p>
							</div>
							<div><img class="mui-pull-right" src="../../assets/img/icon-right.png"/></div>
						</li>
						<li class="mui-clearfix" @click="$router.push({name: 'projectManager'})">
							<div class="mui-pull-left">
								<h3>软件开发</h3>
								<p class="mui-ellipsis">软件开发是根据用户要求建造出软件系统或者系统中的软件部分的过程。</p>
								<p>
									<span class="mui-pull-left">-- 18/05/04</span>
									<span class="mui-pull-right">14:15:15</span>
								</p>
								<p>￥1,066.64 人民币·还剩6天·3天</p>
							</div>
							<div><img class="mui-pull-right" src="../../assets/img/icon-right.png"/></div>
						</li>
					</ul>
				</mt-loadmore>		
			
			</div>
		
		
		<router-link :to="{name: 'publishProject'}">
			<div class="add">+</div>
		</router-link>
		<nav-footer active="myProject"></nav-footer>
	</div>
</template>

<script>
import '../../assets/css/myProject.css'
import NavHeader from '../../components/NavHeader1.vue';
import NavFooter from '../../components/NavFooter.vue';

	export default {
		data() {
			return {
				msgDivDefaultShow: false,
			}
		},
		components: {
			NavHeader,
			NavFooter
		},
		mounted() {
			$(".mint-loadmore").css('height', window.innerHeight);
		},
		methods: {
			loadTop() {
				// 加载数据
			  	this.allLoaded = true;// 若数据已全部获取完毕
			  	this.$refs.loadmore.onBottomLoaded();
			},
		}
	}
</script>

<style>

</style>


