<template>
	<div id="search" style="background-color: white;">
			<!-- 搜索栏 -->
			<header>
				<img src="../../assets/img/search1.png"/>
				<!--<input type="text" placeholder="浏 览" />-->
				<p class="search" @click="$router.push({name: 'search_view'})"><span>浏 览</span></p>
			</header>
			
		<mt-loadmore :top-method="loadTop" ref="loadmore">
			<div class="container">
				<!-- 推荐项目-->
				<ul>
					<li class="ul-header mui-clearfix" @click="$router.push({name: 'recommend_projectList'})">
						<div class="mui-pull-left">
							<h3>推荐项目</h3>
							<p>为你推荐的项目</p>
						</div>
						<img src="../../assets/img/icon-right2.png" class="mui-pull-right" />
					</li>
					<li class="mui-clearfix">
						<router-link :to="{name: 'recommend_detail'}" class="mui-clearfix">
							<div class="mui-pull-left">
								<h3 class="mui-ellipsis">软件开发</h3>
								<p class="mui-ellipsis">￥1,066.64 CNY·还剩6天·3天投标</p>
							</div>
							<img src="../../assets/img/icon-right2.png" class="mui-pull-right" />
						</router-link>
					</li>
					<li class="mui-clearfix">
						<router-link :to="{name: 'recommend_detail'}" class="mui-clearfix">
							<div class="mui-pull-left">
								<h3 class="mui-ellipsis">软件开发</h3>
								<p class="mui-ellipsis">￥1,066.64 CNY·还剩6天·3天投标</p>
							</div>
							<img src="../../assets/img/icon-right2.png" class="mui-pull-right" />
						</router-link>
					</li>
					<li class="mui-clearfix">
						<router-link :to="{name: 'recommend_detail'}" class="mui-clearfix">
							<div class="mui-pull-left">
								<h3 class="mui-ellipsis">软件开发</h3>
								<p class="mui-ellipsis">￥1,066.64 CNY·还剩6天·3天投标</p>
							</div>
							<img src="../../assets/img/icon-right2.png" class="mui-pull-right" />
						</router-link>
					</li>
				</ul>
				
				<!-- 类别 -->
				<ul class="leibie">
					<li class="ul-header mui-clearfix">
						 <div class="mui-pull-left">
							<h3>类别</h3>
							<p>按类别浏览项目</p>
						</div>
					</li>
					<li>
						<router-link :to="{name: 'search_others'}">网页设计，IT软件
							<img src="../../assets/img/icon-right2.png" class="mui-pull-right" />
						</router-link>
					</li>
					<li>
						<router-link :to="{name: 'search_others'}">写作&内容
							<img src="../../assets/img/icon-right2.png" class="mui-pull-right" />
						</router-link>
					</li>
					<li>
						<router-link :to="{name: 'search_others'}">内容，设计&建筑
							<img src="../../assets/img/icon-right2.png" class="mui-pull-right" />
						</router-link>
					</li>
					<li>
						<router-link :to="{name: 'search_others'}">其他
							<img src="../../assets/img/icon-right2.png" class="mui-pull-right" />
						</router-link>
					</li>
				</ul>
				
			</div>
		</mt-loadmore>
		
		<router-link :to="{name: 'publishProject'}">
			<div class="add">+</div>
		</router-link>
		
		<nav-footer active="search"></nav-footer>
	</div>
</template>

<script>
import NavFooter from '@/components/NavFooter.vue'
	export default {
		data () {
			return {}
		},
		components: {
			NavFooter
		},
		mounted() {
			
		},
		methods: {
			loadTop() {
				// 加载数据
			  	this.allLoaded = true;// 若数据已全部获取完毕
			  	this.$refs.loadmore.onBottomLoaded();
			}
		}
	}
</script>

<style scoped="scoped">
li>a {
	display: block;
}
#search {
	padding: 10px;
}
header {
	position: relative;
}
.search {
	width:100%;
	height: 37px;
	font-size: 14px;
	padding-left: 30px;
	border: 1px solid #d2d2d2;
    border-radius: 5px;
    font-family: "黑体";
    box-sizing: border-box;
    line-height: 37px;
    color: #888;
}
header img {
	border: 0;
    width: 14px;
    position: absolute;
    top: 12px;
    left: 10px;
}

.container {
	padding: 11px 5px 50px;
	/*background-color: red;*/
}
.container div.mui-pull-left {
	width: 200px;
}
.container div.mui-pull-left p {
	font-size: 12px;
	color: rgba(28,28,28,.62);
    padding-top: 9px;
    padding-bottom: 8px;
}
.container div.mui-pull-left {
	margin-left: 10px;
}
.ul-header div.mui-pull-left {
	margin-left: 0;
}
.container li {
	border-bottom: 1px solid #f0f0f0;
	margin-bottom: 21px;
    padding-bottom: 12px;
}
.container h3 {
	font-size: 15px;
	font-weight: 600;
}
.ul-header h3 {
	font-size: 22px;
}
.container img.mui-pull-right {
	float: right;
    margin-top: 10px;
    margin-right: 15px;
    width: 15px;
}
.ul-header img.mui-pull-right {
	width: 23px;
    margin-right: 10px;
}

.container p.mui-ellipsis {
	width: 100%;
}
.container ul:nth-child(2){
	margin-top: 10px;
}
.container ul:nth-child(2) li{
	margin-bottom: 0;
}
.container ul li a {
	color: black;
}
.container ul:nth-child(2) li:not(.ul-header) {
	font-size: 12px;
	padding: 0 10px;
}
.leibie a {
	display: block;
    width: 100%;
    height: 42px;
    line-height: 42px;
	text-decoration: none;
}
.leibie img.mui-pull-right {
	margin-right: 6px;
}
</style>



