<template>
	<div id="ProjectDescription" style="background-color: white;">
		<!--<nav-header title="问答" next="true"></nav-header>-->
		<span class="cha" @click="$router.go(-1)">×</span>
		<div id="container">
			<!--<p class="title">回答</p>-->
			<!--<textarea autoHeight="true" class="neirong" @keyup="check" placeholder="请在这里写您的回答内容" ></textarea>-->
			<p contenteditable="true" class="neirong"  @keyup="check">
				<span v-if="description">请在这里写您的回答内容</span>
			</p>
			<footer :class="{'btn-disabled': disabled, 'footer3': true}">
				<a href="javascript:;" v-if="disabled" >发表</a>
				<success-modal title="发表成功" v-if="!disabled">发表</success-modal>
			</footer>
		</div>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader3.vue'
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {
				description: true,
				disabled: true,
			}
		},
		components: {
			NavHeader,
			SuccessModal
		},
		computed: {
			
		},
		mounted() {
			// textarea高度自适应
//			$.fn.autoHeight = function(){    
//		        function autoHeight(elem){
//		            elem.style.height = 'auto';
////		            elem.scrollTop = 0; //防抖动
////		            elem.style.height = elem.scrollHeight + 'px';
//		        }
//		        this.each(function(){
//		            autoHeight(this);
//		            $(this).on('keyup', function(){
//		                autoHeight(this);
//		            });
//		        });     
//		    }                
//		    $('textarea[autoHeight]').autoHeight();    
		},
		methods: {
			next() {
				this.$router.push({name: ''});
			},
			check() {
				var value = $('textarea').val();
				var isTrue = true;
				if(value != "") {
					isTrue = false;
				}else {
					isTrue = true;
				}
				this.disabled = isTrue;
				this.description = isTrue;
			},
		}
	}
</script>

<style scoped="scoped">
.ProjectDescription {
	position: relative;
}
.cha {
	float: right;
    font-size: 40px;
    position: absolute;
    right: 10px;
}
.disabled {
	color: #d9d9d9;
}
#container {
	padding: 40px 25px 0;
}
#container .title {
	font-size: 25px;
    font-weight: bold;
}
#container p:focus {
	outline: 0;
}

.neirong {
	padding: 40px 10px 30px 0;
	line-height: 20px;
    font-size: 14px;
    word-break: break-all;
    border: 0; 
    width: 100%; 
    resize: none;
    font-family: "黑体";
}
.neirong span {
	color: #aaa;
}
.neirong:focus {
	outline: none;
}
</style>


