<template>
	<div id="addPaymentCardForm" style="background-color: white;">
		<header class="mui-clearfix">
			<a @click="$router.go(-1)">
				<img class="mui-pull-left" src="../../assets/img/icon-left2.png" />
				<span class="mui-pull-left">添加银行卡</span>
			</a>
		</header>
		
		<div class="container">
			<ul>
				<li>
					<p class="title">银行卡号</p>
					<input type="number" @keyup.enter="enter" @keyup.delete="del" autofocus="autofocus"/>
					<span @click="show" class="coin">{{card}} <img height="7" src="../../assets/img/icon-down2.png" /></span>
					<div class="chooseDiv">
						<span class="choose" @click="choose">储蓄卡</span>
						<span @click="choose">信用卡</span>
						<span @click="choose">借记卡</span>
					</div>
				</li>
				<li>
					<p class="title">姓名</p>
					<input type="text" @keyup.enter="enter" @keyup.delete="del"/>
				</li>
				<li>
					<p class="title">安全码</p>
					<input type="text" @keyup.enter="enter" @keyup.delete="del"/>
				</li>
				<li>
					<p class="title">有效期</p>
					<input type="text" @keyup.enter="enter" @keyup.delete="del"/>
				</li>
				<li>
					<p class="title">地址</p>
					<input type="text" @keyup.enter="enter" @keyup.delete="del"/>
				</li>
				<li>
					<p class="title">预留电话</p>
					<input type="number" @keyup.enter="enter" @keyup.delete="del"/>
				</li>
			</ul>
			<footer class="footer4">
				<success-modal title="提交成功">提交</success-modal>
			</footer>
		</div>
	</div>
</template>

<script>
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {
				card: '储蓄卡'
			}
		},
		components: {
			SuccessModal
		},
		methods: {
			show(event) {
				$(event.target).next().slideToggle('fast');
			},
			choose(event) {
				$(event.target).addClass('choose').siblings().removeClass('choose');
				this.card = event.target.innerText;
				$('.chooseDiv').slideUp('fast');
			},
			enter(event) {
				$(event.target).parent().next().children("input").focus();
			},
			del(event) {
				$(event.target).parent().prev().children("input").focus();
			}
		}
	}
</script>

<style scoped="scoped">
header {
	width: 100%;
	height: 40px;
	background-color: #0692ef;
	padding-top: 11px;
	box-sizing: border-box;
	padding-left: 25px;
}
header img {
	width: 10px;
	margin-top: 2px;
}
header span {
	font-size: 18px;
	color: white;
	margin-left: 3px;
	margin-top: 3px;
}
.container {
	height: 586px;
	padding: 25px 15px;
	background-color: #0087e0;
	box-sizing: border-box;
}
li {
	position: relative;
	margin-bottom: 19px;
}
li:first-child {
	margin-bottom: 56px;
}
li:last-child {
	padding-bottom: 30px;
}
li .title {
	font-size: 15px;
    color: white;
    font-family: "黑体";
}
li input {
	border: 0;
    padding: 9px 0;
    background-color: transparent;
    border-bottom: 1px solid white;
    width: 100%;
    color: white;
}
li span {
	position: absolute;
    color: white;
    font-size: 12px;
    bottom: 0;
    right: 0;
}
.coin {
	padding: 6px 3px 6px 10px;
    box-sizing: border-box;
    border: 1px solid #e1e1e1;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-bottom: 0;
    margin-top: 0;
    width: 72px;
    text-align: right;
    background-color: white;
    color: #0087e0;
}
.chooseDiv {
    float: right;
    position: absolute;
    top: 53px;
    right: 0;
    border: 1px solid #e1e1e1;
    width: 70px;
    background-color: white;
    display: none;
    z-index: 100;
    text-align: right;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top: 0;
}
.chooseDiv span{
	position: static;
	display: block;
    padding: 4px 16px 4px 5px;
    border-bottom: 1px solid #eee;
    color: #999;
    font-weight: normal;
    font-size: 12px;
}
.chooseDiv span:last-child {
	border-bottom: 0;
}
.chooseDiv span.choose {
	border-top: 1px solid #e1e1e1;
	border-radius: 0;
	color: white;
	background-color: #addaf6;
}
</style>


