<template>
	<div style="background-color: white;">
		<header>
			<img width="10" src="../../assets/img/icon-left.png" @click="$router.go(-1)"/>
			<span>项目管理</span>
		</header>
		
		<ul class="container">
			<!-- 项目详细信息 -->
			<li class="mui-clearfix">
				<div :class="{'circle': isCircle_1, 'circle-success': !isCircle_1}" @click="isCircle_1=!isCircle_1"></div>
				<div class="mui-pull-left">
					<h2>项目详细信息</h2>
					<p class="mui-clearfix">
						<span class="mui-pull-left">你的项目详情</span>
						<span class="mui-pull-right time">18/02/05 14:26:50</span>
					</p>
					<div class="neirong">
						Google钦定的 Android 官方一级开发语言，未来主流，现 在不学，更待何时课程将从语特征到各种 Kotlin 新特性Google钦定的 Android 官方一级开发语言，未来主流，现 在不学，更待何时课程将从语特征到各种 Kotlin 新特性
					</div>
					<span class="mui-pull-right more">项目详情 <img src="../../assets/img/icon-more2.png" width="9" /></span>
					<p style="font-size: 14px;">￥80人名币·1周·1人投标</p>
				</div>
				<img src="../../assets/img/icon-down2.png" class="mui-pull-right icon-more" @click="more(lisHeight[0], 0)" />
			</li>
			
			<!-- 约定明细 -->
			<li class="mui-clearfix">
				<div :class="{'circle': isCircle_2, 'circle-success': !isCircle_2}" @click="isCircle_2=!isCircle_2"></div>
				<div class="mui-pull-left">
					<h2>约定明细</h2>
					<p>查看双方最终的约定明细</p>
					
					<p class="file">精简版文档模板</p>
					<img src="../../assets/img/icon-file.png" width="25px" class="mui-pull-left"/>
					<span class="mui-pull-right">
						<a style="font-size: 12px; color: #0087e0; text-decoration: underline; border-bottom: 1px solid #0087e0; margin-right: 20px;" href="javascript:;">下载</a>
						<a style="font-size: 12px; color: #0087e0; text-decoration: underline; border-bottom: 1px solid #0087e0;" href="javascript:;">在线预览</a>
					</span>
				</div>
				<img src="../../assets/img/icon-down2.png" class="mui-pull-right icon-more" @click="more(lisHeight[1], 1)" />
			</li>
			<!-- 管理 -->
			<li class="mui-clearfix" id="manager">
				<div :class="{'circle': isCircle_3, 'circle-success': !isCircle_3}" @click="isCircle_3=!isCircle_3"></div>
				<div class="mui-pull-left">
					<h2>管理</h2>
					<p>管理您的项目流程</p>
					
					<div class="liucheng">
						<div class="grid mui-clearfix" v-for="(item, index) in liuchengs" :key="index">
							<div id="box" class="mui-pull-left">
								{{index+1}}
								<confirm-model title="确定要添加新流程？" cancelButtonText="否" confirmButtonText="是" v-on:confirm="confirm">
									<img src="../../assets/img/icon-add1.png"/>
								</confirm-model>
							</div>
							<div class="mui-pull-left" style="width: 70%; margin-left: 15px;">
								<h3 style="font-size: 23px; font-weight: bold;">{{item.title}}</h3>
								<h4 style="font-size: 14px; padding-top: 10px;">{{item.datetime}}</h4>
							</div>
						</div>
						
					</div>
				</div>
				<img src="../../assets/img/icon-down2.png" class="mui-pull-right icon-more" @click="more(lisHeight[2], 2)" />
			</li>
			<!-- 留下评论 -->
			<li class="mui-clearfix disabled">
				<div v-if="disabled" :class="{'circle': isCircle_4, 'circle-success': !isCircle_4}" @click="isCircle_4=!isCircle_4"></div>
				<div v-if="!disabled" class="circle circle-disabled"></div>
				<div class="mui-pull-left">
					<h2>留下评论</h2>
					<p>让每一个人知道这个开发者工作怎样</p>
					
					<p style="width: 100%; height: 130px; background-color: #e5e4e4; margin-top: 20px;" contenteditable="true"></p>
					<a style="float: right; font-size: 12px; color: #0087e0; text-decoration: underline; border-bottom: 1px solid #0087e0; margin-top: 10px;" href="javascript:;">提交</a>
				</div>
				<img v-if="disabled" src="../../assets/img/icon-down2.png" class="mui-pull-right icon-more" @click="more(lisHeight[3], 3)" />
				<img v-if="!disabled" src="../../assets/img/icon-down3.png" class="mui-pull-right icon-more" />
			</li>
		</ul>
	</div>
</template>

<script>
import ConfirmModel from '@/components/Modal/ConfirmModel.vue'
import {spread_auto} from '@/assets/js/OtherInfoAbout.js'
	export default {
		data() {
			return {
				isCircle_1: true,
				isCircle_2: true,
				isCircle_3: true,
				isCircle_4: true,
				disabled: false,
				lisHeight: [],
				liuchengs: [
					{title: '合作达成', datetime: '2014.02.02'}
				]
			}
		},
		components: {
			ConfirmModel
		},
		mounted() {
			this.initHeight();
		},
		methods: {
			more(height, index) {
				spread_auto (height, index, 50)
			},
			initHeight() {
				var lis_height = [];
				$('li').each(function(index, item) {
					lis_height.push($(this).height());
					$(this).css('height', "50px")
				})
				this.lisHeight = lis_height;
			},
			confirm() {
				this.add();
			},
			add() {
				var obj = {
					title: '项目1',
					datetime: "2014.05.06"
				}
				this.liuchengs.push(obj);
				var height = document.getElementById("manager").scrollHeight;
				$('#manager').css("height", height);
			}
		}
	}
</script>

<style scoped="scoped">
header {
	width: 100%;
	height: 40px;
	padding: 10px 10px 20px;
	box-sizing: border-box;
}
header img {
	margin-top: 5px;
}
header span {
	margin-left: 10px;
	font-size: 18px;
	font-weight: bold;
	line-height: 40px;
}
.grid {
    padding-bottom: 65px;
}
.circle {
	width: 13px;
	height: 13px;
	border: 2px solid #72cc72;
	border-radius: 50%;
	float: left;
	margin-top: 15px;
	margin-right: 16px;
    margin-left: 3px;
}
.circle-disabled {
	border-color:#dcdcdc ;
}
.circle-success {
	width: 26px;
	height: 26px;
	background: url(../../assets/img/icon-success.png) no-repeat center center;
	border-radius: 50%;
	background-size: 26px;
	margin-top: 10px;
	float: left;
	margin-right: 10px;
}
.container {
	padding-top: 15px;
}
.container li {
	padding: 20px 20px;
	border-bottom: 1px solid #dedede;
	overflow: hidden;
}
.container img.mui-pull-right {
	width: 16px;
	margin-top: 20px;
}
li h2 {
	font-size: 24px;
    font-weight: bold;
}
li p {
    font-size: 12px;
    padding-top: 22px;
    width: 92%;
}
.container li div.mui-pull-left {
	width: 80%;
}
.disabled {
	background-color: #f3f3f3;
	color: #aaaaaa;
}
.neirong {
    margin-top: 13px;
    font-size: 12px;
    line-height: 15px;
    word-break: break-all;
}
span.more {
	font-size: 12px;
}
.liucheng {
	margin-top: 20px;
	padding-bottom: 190px;
}
#box {
    width: 45px;
    height: 45px;
    border-radius: 5px;
    background-color: #aaaaaa;
    position: relative;
    text-align: center;
    line-height: 45px;
    color: white;
    font-size: 25px;
}
#box img {
	width: 24px;
	position: absolute;
    top: 45px;
    left: 11px;
}
.file {
	width: 100%; 
	height: 277px; 
	background-color:#e5e4e4; 
	margin: 20px 0 5px; 
	padding: 10px; 
	box-sizing: 
	border-box; 
	overflow: auto;
}
</style>


