<template>
	<div style="position: relative;background-color: white;">
		<nav-header></nav-header>
		
		<div class="container">
			<p class="title">第三方登陆</p>
			<a class="weixin" href="javascript:;"><img src="../../assets/img/icon-weixin.png" /> 微信</a>
			<p class="title">或者用你的</p>
			<input type="email" name="email" placeholder="邮箱" @focus="toLogin" />
		</div>	
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader5.vue'
	export default {
		data() {
			return {}
		},
		components: {
			NavHeader
		},
		mounted() {
			$('.container').css('height', (window.innerHeight-40));
		},
		methods: {
			toLogin() {
				this.$router.push({name: 'login'})
			}
		}
	}
</script>

<style scoped="scoped">
.container {
	padding: 75px 30px 0;
	background-color: #0087e0;
	box-sizing: border-box;
    font-family: "黑体";
}
.container .title {
	font-size: 14px;
	color: #5fb3eb;
}
.container .weixin {
	display: block;
	margin: 13px 0 33px;
	width: 100%;
	height: 50px;
	text-align: center;
	line-height: 50px;
	border: 1px solid white;
	border-radius: 3px;
	color: white;
}
.weixin img {
	height: 30px;
    vertical-align: middle;
}
.container input {
	width: 100%;
    border: 0;
    background-color: transparent;
    color: white;
    margin-top: 45px;
    padding-bottom: 17px;
    margin-bottom: 38px;
    border-bottom: 1px solid white;
    font-size: 14px;
}
input::-webkit-input-placeholder { /* WebKit browsers */ 
	color: white; 
} 
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */ 
	color: white; 
} 
input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
	color: white; 
} 
input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
	color: white; 
}
</style>


