import Vue from 'vue'
import Router from 'vue-router'
import First from '@/views/LoginOrRegist/first.vue'
import BeforeRegist from '@/views/LoginOrRegist/BeforeRegist.vue'
import Regist from '@/views/LoginOrRegist/Regist.vue'
import BeforeLogin from '@/views/LoginOrRegist/BeforeLogin.vue'
import Login from '@/views/LoginOrRegist/Login.vue'
import ForgetPwd from '@/views/LoginOrRegist/ForgetPwd.vue'

import Home from '@/views/Home.vue'
import Home_History from '@/views/Home_History.vue'
import MsgDetail from '@/views/msg/MsgDetail.vue'

import MyProject from '@/views/myProject/MyProject.vue'
import MyProject_Complete from '@/views/myProject/MyProject_Complete.vue'
import PublishProject from '@/views/myProject/PublishProject.vue'
import ProjectRequire from '@/views/myProject/ProjectRequire.vue'
import ProjectBudget from '@/views/myProject/ProjectBudget.vue'
import ProjectDescription from '@/views/myProject/ProjectDescription.vue'
import ProjectInfo from '@/views/myProject/ProjectInfo.vue'
import SelectSkill from '@/views/myProject/SelectSkill.vue'
import ProjectManager from '@/views/ProjectManager/ProjectManager.vue'

import Search from '@/views/search/Search.vue'
import SearchView from '@/views/search/SearchView.vue'
import recommendDetail from '@/views/search/recommendDetail.vue'
import RecommendProjectList from '@/views/search/RecommendProjectList.vue'
import SearchBid from '@/views/search/SearchBid.vue'
import SearchOthers from '@/views/search/SearchOthers.vue'
import SearchOthersAsk from '@/views/search/SearchOthersAsk.vue'
import SearchOthersProductDetail from '@/views/search/SearchOthersProductDetail.vue'

import Bid from '@/views/search/Bid.vue'

import OtherInfo from '@/views/otherInfo/OtherInfo.vue'
import OtherInfoView from '@/views/otherInfo/OtherInfoView.vue'
import OtherInfo_ProductListPublish from '@/views/otherInfo/OtherInfo_ProductListPublish.vue'
import OtherInfo_ProductListBought from '@/views/otherInfo/OtherInfo_ProductListBought.vue'
import OtherInfoProductDetail_Pulish from '@/views/otherInfo/OtherInfoProductDetail_Pulish.vue'
import OtherInfoProductDetail_Bought from '@/views/otherInfo/OtherInfoProductDetail_Bought.vue'

import Introduction from '@/views/mine/Introduction.vue'
import ID_Authentication from '@/views/mine/ID_Authentication.vue'
import MineInfo from '@/views/mine/MineInfo.vue'
import MineInfoView from '@/views/mine/MineInfoView.vue'
import MineInfo_ProductListPublish from '@/views/mine/MineInfo_ProductListPublish.vue'
import MineInfo_ProductListBought from '@/views/mine/MineInfo_ProductListBought.vue'
import MineInfoProductDetail_Pulish from '@/views/mine/MineInfoProductDetail_Pulish.vue'
import MineInfoProductDetail_Bought from '@/views/mine/MineInfoProductDetail_Bought.vue'
import MineInfo_Answer from '@/views/mine/MineInfo_Answer.vue'
import Mine_PublishProject from '@/views/mine/PublishProject.vue'
import ShareView from '@/views/mine/ShareView.vue'
import Balace from '@/views/mine/Balace.vue'
import Recharge from '@/views/mine/Recharge.vue'
import Tixian from '@/views/mine/Tixian.vue'
import AddPaymentCard from '@/views/mine/addPaymentCard.vue'
import AddPaymentCardForm from '@/views/mine/AddPaymentCardForm.vue'

import SelectProjectType from '@/views/HireMe/SelectProjectType.vue'
import ConfirmPrice from '@/views/HireMe/ConfirmPrice.vue'
import HireProjectDescription from '@/views/HireMe/HireProjectDescription.vue'

import Question from '@/views/Question.vue'

import QuestionDetail from '@/views/Question/QuestionDetail.vue'
import Answer from '@/views/Question/Answer.vue'
import Response from '@/views/Question/Response.vue'


//import Milestone from '@/views/msg/Milestone.vue'
import PayView from '@/views/PayView.vue'
import Modal from '@/views/Modal.vue'

Vue.use(Router);

export default new Router({
  	routes: [
	    {
	      	path: '/first',		// 登录和注册之前的界面
	      	name: 'first',
	      	component: First
	    },
	    {
	      	path: '/beforeRegist',		// 注册之前的页面
	      	name: 'beforeRegist',
	      	component: BeforeRegist
	    },
	    {
	      	path: '/regist',		// 注册
	      	name: 'regist',
	      	component: Regist
	    },
	    {
	      	path: '/beforeLogin',		// 登录之前的页面
	      	name: 'beforeLogin',
	      	component: BeforeLogin
	    },
	    {
	      	path: '/login',		// 登录
	      	name: 'login',
	      	component: Login
	    },
	    {
	      	path: '/forgetPwd',		// 忘记密码
	      	name: 'forgetPwd',
	      	component: ForgetPwd
	    },
	    {
	      	path: '/',			// 首页
	      	name: 'Home',
	      	component: Home
	    },
	    {
	      	path: '/home_histroy',		// 首页_历史
	      	name: 'Home_histroy',
	      	component: Home_History
	    },
	    {
	      	path: '/msg/detail',			// 信息-详情
	      	name: 'msg_detial',
	      	component: MsgDetail
	    },
	    {
	      	path: '/myProject',				// 我的项目
	      	name: 'MyProject',
	      	component: MyProject
	    },
	    {
	      	path: '/myProject_complete',				// 我的项目_已完成项目
	      	name: 'MyProject_Complete',
	      	component: MyProject_Complete
	    },
	    {
	    	path: '/publish/project',				// 发布项目
	      	name: 'publishProject',
	      	component: PublishProject
	    },
	    {
	    	path: '/publish/project/require',				// 发布项目-项目需求
	      	name: 'projectRequire',
	      	component: ProjectRequire
	    },
	    {
	    	path: '/publish/project/require/budget',				// 发布项目-项目需求-项目预算
	      	name: 'projectBudget',
	      	component: ProjectBudget
	    },
	    {
	    	path: '/publish/project/require/budget/description',				// 发布项目-项目需求-项目预算-项目说明
	      	name: 'projectDescription',
	      	component: ProjectDescription
	    },
	    {
    		path: '/publish/project/require/budget/description/info',				// 发布项目-项目需求-项目预算-项目说明
	      	name: 'projectInfo',
	      	component: ProjectInfo
	    },
	    {
    		path: '/publish/project/require/budget/description/selectSkill',				// 发布项目-项目需求-项目预算-项目说明
	      	name: 'selectSkill',
	      	component: SelectSkill
	    },
	    {
    		path: '/project/projectManager',				// 我的项目-项目管理
	      	name: 'projectManager',
	      	component: ProjectManager
	    },
	    {
	    	path: '/search',					// 搜索
	    	name: 'Search',
	    	component: Search
	    },
	    {
		  	path: '/search/view',		// 搜索页面
		  	name: 'search_view',
		  	component: SearchView
		},
		{
	    	path: '/search/recommend/projectList',		// 搜索-推荐项目-列表
	    	name: 'recommend_projectList',
	    	component: RecommendProjectList,
	    }, 
		{
	    	path: '/search/recommend/detail',		// 搜索-推荐项目-详情
	    	name: 'recommend_detail',
	    	component: recommendDetail
	    },
		{
	    	path: '/search/recommend/detail/bid',		// 搜索-推荐项目-详情-投标
	    	name: 'recommend_detail_bid',
	    	component: SearchBid
//	    	beforeEnter: (to, from, next) => {
//	        	next()
//	      	}
	    },
	    {
    		path: '/search/others',			// 搜素-其他
	      	name: 'search_others',
	      	component: SearchOthers,
	    },
	    {
			path: '/search/others/question/ask', 	// 搜索-其他-成品-问答
		  	name: 'search_others_ask', 
		  	component: SearchOthersAsk
		},
		{
			path: '/search/others/product/detail', 	// 搜索-其他-成品-详情
		  	name: 'search_others_product_detail', 
		  	component: SearchOthersProductDetail
		},	
		{
    		path: "/bid",			// 投标质量得分
    		name: "bid",
    		component: Bid
	    },
	    {
	    	path: '/otherInfo',				// 别人的信息页面
	    	name: 'OtherInfo',
	    	component: OtherInfo,
	    	children: [
		      	{
		      		name: 'otherInfo_view', 
		      		path:'view', 
		      		component:OtherInfoView,
		      		children: [
		      			{name: 'otherInfo_productList_publish', path:'productList/publish', component:OtherInfo_ProductListPublish},	//已发布
						{name: 'otherInfo_productList_bought', path:'productList/bought', component:OtherInfo_ProductListBought},		// 已买
						{name: 'otherInfo_product_publish_detail', path:'product/publish/detail', component:OtherInfoProductDetail_Pulish},	// 已发布-成品详情
		      			{name: 'otherInfo_product_bought_detail', path:'product/bought/detail', component:OtherInfoProductDetail_Bought}	// 已买-成品详情
		      		],
		      		redirect: 'view/productList/publish'		// 这里的重定向不要otherInfo！
		      	},
		      	
	      	],
	      	redirect: '/otherInfo/view'
	    },
	    {
    		path: "/ID_Authentication",	// 身份认证
    		name: "ID_Authentication",
    		component: ID_Authentication
	    },
	    {
    		path: "/introduction",	//我的-介绍
    		name: "introduction",
    		component: Introduction
	    },
	    {
    		path: "/balace",			// 我的-介绍-余额
    		name: "balace",
    		component: Balace
	    },
	    {
    		path: "/recharge",			// 我的-介绍-余额-充值
    		name: "recharge",
    		component: Recharge
	    },
	    {
    		path: "/tixian",			// 我的-介绍-余额-提现
    		name: "tixian",
    		component: Tixian
	    },
	    {
    		path: "/addPaymentCard",			// 我的-介绍-余额-充值-添加银行卡
    		name: "addPaymentCard",
    		component: AddPaymentCard
	    },
	    {
    		path: "/addPaymentCardForm",			// 我的-介绍-余额-充值-添加银行卡-添加银行卡表格
    		name: "addPaymentCardForm",
    		component: AddPaymentCardForm
	    },
	    {
	    	path: '/mineInfo',				// 我的信息页面
	    	name: 'mineInfo',
	    	component: MineInfo,
	    	children: [
		      	{
		      		name: 'mineInfo_view', 
		      		path:'view', 
		      		component:MineInfoView, 
		      		children: [
						{name: 'mineInfo_productList_publish', path:'productList/publish', component:MineInfo_ProductListPublish},
						{name: 'mineInfo_productList_bought', path:'productList/bought', component:MineInfo_ProductListBought},
						{name: 'mineInfo_product_publish_detail', path:'product/publish/detail', component:MineInfoProductDetail_Pulish},
		      			{name: 'mineInfo_product_bought_detail', path:'product/bought/detail', component:MineInfoProductDetail_Bought}
		      		],
		      		redirect: 'view/productList/publish'		// 这里的重定向不要mineInfo！
		      	},
		      	{ path: 'answer', name: 'mineInfo_answer', component: MineInfo_Answer}
		      	
	      	],
	      	redirect: '/mineInfo/view'
	    },
	    {
    		path: "/mine/publishProject",			// 我的信息页面-成品-发布
    		name: "mine_publishProject",
    		component: Mine_PublishProject
	    },
    	{
    		path: "/shareView",			// 我的-介绍-分享
    		name: "shareView",
    		component: ShareView
	    },
		{
    		path: "/hire/selectProjectType",			// 聘用我-选择项目类型
    		name: "hire_selectProjectType",
    		component: SelectProjectType
	    },
	    {
    		path: "/hire/confirmPrice",			// 聘用我-选择项目类型-确认项目价格
    		name: "hire_confirmPrice",
    		component: ConfirmPrice
	    },
	    {
    		path: "/hire/projectDescription",			// 聘用我-选择项目类型-确认项目价格-聘请
    		name: "hire_projectDescription",
    		component: HireProjectDescription
	    },
	    {
    		path: "/question",			// 投标常见问题
    		name: "bid_question",
    		component: Question
	    },
//	    {
//  		path: "/milestone",			// 里程碑
//  		name: "milestone",
//  		component: Milestone
//	    },
		{
    		path: "/modal",			// Modal
    		name: "modal",
    		component: Modal
	    },
	    {
    		path: "/payView",			// 支付页面
    		name: "payView",
    		component: PayView
	    },
	    {
		  	path: '/questionDetail',		// 问答评论
		  	name: 'questionDetail',
		  	component: QuestionDetail,
		  	children: [
				{name: 'answer', path:'answer', component:Answer},
      			{name: 'response', path:'response', component:Response}
		    ],
		  	redirect: '/questionDetail/answer'
		}
	]
})



