<template>
	<div style="background-color: white;">
		<!--<nav-header title="问答"></nav-header>-->
			<p class="nav">
				<router-link :to="{name: 'mineInfo_productList_publish'}" class="active">已发布</router-link>
				<router-link :to="{name: 'mineInfo_productList_bought'}">已买</router-link>
			</p>
				<li class="mui-clearfix">
					<div class="subnav">
						<span>详细信息</span>
					</div>
					
					<div class="skillGroup mui-clearfix">
						<div class="skillDiv">
							<span></span>
							<span>PHP</span>
						</div>
						<div class="skillDiv">
							<span></span>
							<span>芝梦</span>
						</div>
					</div>
					
					<div class="neirong1">
						你能通过信息联系到我：<br />
						还有：你也能通过qq，微信，微博，邮件来联系我，<br />
						你能通过信息联系到我：<br />
						还有：你也能通过qq，微信，微博，邮件来联系我，<br />
						你能通过信息联系到我：<br />
						还有：你也能通过qq，微信，微博，邮件来联系我，<br />
						你能通过信息联系到我：<br />
						还有：你也能通过qq，微信，微博，邮件来联系我，<br />
						<div class="spread" @click="spread(156)">
							<span class="mui-pull-left">阅读全文</span>
							<img class="mui-pull-right toggle" src="../../assets/img/icon-down.jpg" />
						</div>
					</div>
				</li>
	</div>
</template>

<script>
import {spread} from '@/assets/js/OtherInfoAbout.js';
import NavHeader from '@/components/NavHeader3.vue'
	export default {
		data() {
			return {
				height: 0
			}
		},
		components: {
			NavHeader
		},
		mounted() {
			this.initHeight();
			
			
		},
		methods: {
			spread(h) {
					if ($('.neirong1').height() <= 100) {
						$('.neirong1').animate({height: h}, 500);
						$(".neirong1 .toggle").css("transform", 'rotate(180deg)');
					}else if($('.neirong1').height() >= h) {
						$('.neirong1').animate({height: "100px"}, 500);
						$(".neirong1 .toggle").css("transform", 'rotate(360deg)');
					}
			},
			initHeight() {
				// 初始化折叠框的height
				$('.neirong1').css("height", 100)
			}
		}
	}
</script>

<style scoped="scoped">
.nav {
    margin-top: -26px;
}
.nav a {
	font-size: 13px;
    color: #8e8e8e;
    text-decoration: none;
    font-weight: bold;
    margin-right: 20px;
}
.nav a.active {
	color: black;
}
.container{
	padding: 0 10px;
}
.container ul {
	padding: 0 10px;
	border: 1px solid #dedede;
	border-radius: 10px;
	position: relative;
}
.container li {
	padding: 17px 0;
	border-bottom: 1px solid #dedede;
	margin-bottom: 10px;
	overflow: hidden;
}
.container li:last-child {
	border: 0;
}
h3 {
	margin: 10px 0;
}
h3 a{
	font-size: 13px;
    color: #8e8e8e;
    text-decoration: none;
    font-weight: bold;
    margin-right: 20px;
}
.is-selected {
	color: black;
}
.subnav {
    border-bottom: 1px solid #f0f0f0;
    padding-bottom: 13px;
}
.subnav span {
	font-size: 11px;
    padding-bottom: 10px;
    color: #0087e0;
    font-weight: bold;
    border-bottom: 3px solid #0087e0;
}
.skillGroup {
	margin-top: 17px;
	margin-bottom: 26px;
}
.skillDiv {
	width: 50px;
    height: 20px;
    border: 1px solid #dcdcdc;
    border-radius: 3px;
    margin-right: 20px;
    float: left;
}
.skillDiv span {
	float: left;
    height: 100%;
    display: inline-block;
}
.skillDiv span:first-child {
	width: 16px;
    background-color: #00ff00;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}
.skillDiv span:last-child {
	width: 34px;
    font-size: 11px;
    float: left;
    line-height: 22px;
    text-align: center;
    font-weight: 500;
}
.neirong1 {
	width: 100%;
    /*padding-bottom: 40px;*/
    font-size: 12px;
    float: left;
    line-height: 16px;
    overflow: hidden;
}

</style>


