<template>
	<div>
		<a href="javascript:;" @click="show">
			<slot></slot>
		</a>
	</div>
</template>

<script>
import { MessageBox } from 'mint-ui';
	export default {
		data () {
			return {
				
			}
		},
		methods: {
			show() {
				var htmls =  '<div style="width:100%; height:100%;overflow:hidden;padding-top: 40px;">'+
								'<div style="width:100%;height: 40px;background-color: #1793c7;line-height: 40px;position: absolute;left:0; top:0;color: white;">客服： NO.333333</div>'+
				              	'<p style="font-size: 14px; text-align:left; line-height: 20px; color: #0087e0;">客服555555522很高兴为您服务，专长xxxx </br>xxxxxxxxxxx</br> xxxx </br>xxx</br> xxx</br> xx</p>'+  
				              "</div>";
	        	
	    		MessageBox({
				  	title: '',
				  	message: htmls,
	        		showModal: false,
				  	confirmButtonText: '联系他'
				});
			}
				
		}
	}
</script>

