<template>
	<div id="SearchBid" style="background-color: white;">
		<header>
			<a href="javascript:;" @click="$router.go(-1)">×</a>
		</header>
		
		<div class="container">
			<h1>投标</h1>
			<h3>香港兼职会计</h3>
			
			<ul>
				<li>
					<p class="title">薪资</p>
					<div class="inputGroup mui-clearfix">
						<input type="number" class="mui-pull-left" />
						<button>人民币</button>
					</div>
					<p class="alert">雇主合计支付：￥6666元。（手续费5%合计500人名 币）,<i>投标人须缴纳雇主合计支付得5%为：200人民币</i></p>
				</li>
				<li>
					<p class="title">交付时间</p>
					<div class="inputGroup mui-clearfix">
						<input type="number" class="mui-pull-left" />
						<button>天</button>
					</div>
				</li>
			</ul>
			
			<div class="fenshuDiv">
				<div>
					<p>8/<small>8</small></p>
					<p>建议</p>
				</div>
				<div>
					<p style="color: #4cb958;">
						<span style="vertical-align: middle;">100</span>
						<img @click="$router.push({name: 'bid'})" style="width: 13px;" src="../../assets/img/icon-question.png" />
					</p>
					<p>质量分</p>
				</div>
			</div>
			
		</div>
		
		<footer class="footer-btn">
			<error-modal v-if="!is_success">投标</error-modal>
			<success-modal title="投标成功" v-if='is_success'>投标</success-modal>
		</footer>
	</div>
</template>

<script>
import ErrorModal from '@/components/Modal/ErrorModal.vue'
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {
				is_success: false,		// 是否投标成功
			}
		},
		beforeRouteEnter(to, from, next) {
			if(from.name == 'bid_question') {
				next(vm => {
					location.reload();
				});
			}else {
				next();
			}
				
		},
		components: {
			ErrorModal,
			SuccessModal
		}
	}
</script>

<style scoped="scoped">
#SearchBid {
	padding: 20px 15px;
	font-family: "黑体";
	position: relative;
	
}
header a {
	font-size: 28px;
    color: #9b9b9b;
}
.container {
    padding-top: 36px;
}
.container h1 {
    font-size: 21px;
    padding-bottom: 17px;
}
.container h3{
	font-size: 15px;
	padding-bottom: 35px;
}
.container li {
	margin-bottom: 23px;
}
li .title {
    font-size: 15px;
}
.inputGroup {
    margin-top: 17px;
    position: relative;
}
.inputGroup input {
	padding: 15px 18px;
    border: 1px solid #ecedef;
    font-size: 16px;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}
.inputGroup button {
	border: 0;
    width: 100px;
    position: absolute;
    right: 0;
    height: 51px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    font-size: 16px;
    font-family: "黑体";
}
.alert {
	font-size: 12px;
	color: #444b54;
	padding-bottom: 2px;
    padding-top: 5px;
    line-height: 14px;
}
.alert i {
	text-decoration: underline;
}
.fenshuDiv {
	width: 100%;
	height: 100px;
	border: 1px solid #ecedef;
	border-radius: 10px;
	padding: 21px 13px;
	box-sizing: border-box;
	margin-bottom: 35px;
}
.fenshuDiv div {
	display: inline-block;
	border-right: 1px solid #ecedef;
	height: 100%;
	width: 48%;
	box-sizing: border-box;
	text-align: center;
}
.fenshuDiv div:last-child {
	border: 0;
}
.fenshuDiv p {
	padding-bottom: 5px;
}
.fenshuDiv div p:first-child {
    font-size: 19px;
    font-weight: bold;
}
.fenshuDiv small {
	font-size: 14px;
}
.footer-btn a {
	height: 28px;
    position: static;
    border-radius: 5px;
    line-height: 28px;
    font-size: 16px;
}
</style>


