<template>	<!--有title，没有右边的，返回为退后一步-->
	<div>
		<mt-header :title="title" class="navHeader3"> 
		  <a href="javascript:;" slot="left" @click="goback">
		    <mt-button icon="back">返回</mt-button>
		  </a>
		</mt-header>
	</div>
</template>

<script>
	export default {
		props: ['title'],
		data() {
			return {}
		},
		methods: {
			goback () {
				this.$router.go(-1);
			}
		}
	}
</script>



