<template>
	<div style="position: relative;background-color: white;">
		<nav-header></nav-header>
		
		<div class="container">
			<h1>选择你想做的事</h1>
			<h3>不用担心，后面可以改变的</h3>
			
			<router-link :to="{name: 'regist'}">我想成为开发者</router-link>
			<router-link :to="{name: 'regist'}">我想发布任务</router-link>
		</div>
		
		<p class="footer">注册表示我同意服务条款</p>
		
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader5.vue'
	export default {
		data() {
			return {}
		},
		components: {
			NavHeader
		},
		mounted() {
			$('.container').css('height', (window.innerHeight-40));
		}
	}
</script>

<style scoped="scoped">
.container {
	padding: 47px 35px 0;
	background-color: #0087e0;
	color: white;
    box-sizing: border-box;
}
.container h1 {
	font-size: 24px;
	font-weight: bold;
}
.container h3 {
	font-size: 18px;
	padding-top: 18px;
	padding-bottom: 60px;
	font-weight: lighter;
}
.container a {
	display: block;
	width: 100%;
	height: 47px;
	font-size: 18px;
	color: white;
	text-align: center;
	line-height: 47px;
	border: 1px solid white;
	margin-bottom: 20px;
	font-weight: lighter;
}
p.footer {
	width: 100%;
	font-size: 12px;
	text-align: center;
	position: absolute;
	bottom: 10px;
	color: white;
}
</style>


