<template>
	<div style="background-color: white;">
		<nav-header title="邀请朋友"></nav-header>
		<div class="container">
			<div class="mui-clearfix">
				<h1 class="mui-pull-left">成功邀请<br />好友入驻各得 20人民币</h1>
				<img width="120" src="../../assets/img/invite.png" class="mui-pull-right" />
			</div>
			<p>邀请您的自由职业者朋友，在他们交付第 一个任务之后您将会获得2人民币</p>	
			<button @click="showAction">分享您的链接</button>
		</div>
		
		<!-- actionSheet-->
		<van-actionsheet v-model="show" title="分享">
		 	<span style="font-size: 50px; top: 14px;" class="iconfont icon-QQ">
        		<span class="font">QQ</span>
        	</span>
        	<span style="font-size: 50px; top: 14px;" class="iconfont icon-weixin">
        		<span class="font">微信</span>
        	</span>
        	<span class="iconfont icon-xinlangweibo" style="font-size: 56px;top: 16px;">
        		<span class="font" style="top: 59px;">微博</span>
        	</span>
        	<span class="iconfont icon-facebookfacebook52" style="font-size: 48px; top: 13px;">
        		<span class="font" style="margin-left: -26px;top: 54px;">Facebook</span>
        	</span>
		</van-actionsheet>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader3.vue'
	export default {
		data() {
			return {
				show: false,
				actions: [{ name: '选项', loading: true }],
			}
		},
		components: {
			NavHeader,
		},
		methods: {
			showAction() {
				this.show = true;
			}
			
		}
	}
</script>

<style scoped="scoped">
.iconfont {
	font-size: 50px;
	padding: 0 3.5%;
	margin-top: 10px;
    position: relative;
    top: 14px;
}
.font {
	font-size: 12px;
    color: #aaa;
    position: absolute;
    top: 56px;
    left: 50%;
    margin-left: -10px;
}
.container {
	padding: 80px 22px;
}
.container h1 {
    float: left;
    display: block;
    width: 55%;
    vertical-align: top;
    font-size: 24px;
    font-family: "黑体";
    line-height: 27px;
}
.container p {
	font-size: 13px;
    padding-top: 25px;
    padding-bottom: 35px;
    line-height: 17px;
}
.container button {
	width: 180px;
	height: 55px;
	background-color: #0089e7;
	border: 0;
	border-radius: 3px;
	color: white;
}
button:focus {
	outline: 0;
}
.invitation {
	margin-top: 45px;
	padding-bottom: 22px;
	border-bottom: 1px solid #f0f0f0;
}
.invitation span {
	font-size: 13px;
	font-weight: bold;
}
</style>


