<template>
	<div style="background-color: white;">
		<nav-header title="" next="true">
	        <span v-if="next" @click="next">Next</span>
		</nav-header>
		<hr />
		
		<div class="container">
			<h1>确认项目价格</h1>
			<p>为您的项目设置预算和选择币种</p>
			
			<button class="mui-pull-left">人民币 <img src="../../assets/img/icon-down2.png"/></button>
			<input type="number" class="mui-pull-left" value="80" />
		</div>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader4.vue'
	export default {
		data() {
			return {}
		},
		components: {
			NavHeader
		},
		methods: {
			next() {
				this.$router.push({name: 'hire_projectDescription'});
			}
		}
	}
</script>

<style scoped="scoped">
hr {
	position: absolute;
    width: 50%;
    height: 2px;
    border: 0;
    background: #009cdd;
    top: 32px;
}
.container {
	padding: 80px 25px 0;
}	
.container h1 {
	font-size: 24px;
	font-family: "微软雅黑";
	font-weight: bold;
}
.container p {
	font-size: 15px;
	padding-top: 55px;
    font-family: "黑体";
    padding-bottom: 30px;
}
.container button {
    border: 1px solid #e3e3e3;
    background-color: transparent;
    padding: 10px;
    border-radius: 10px;
    font-size: 22px;
    color: #595959;
    font-family: "\9ED1\4F53";
    vertical-align: middle;
}
.container button img {
	width: 10px;
    padding-left: 6px;
    vertical-align: middle;
    margin-top: -3px;
}
input {
    font-size: 48px;
    width: 56%;
    border: 0;
    border-bottom: 1px solid #e3e3e3;
    margin-top: -12px;
    margin-left: 3px;
    background-color: transparent;
    color: #595959;
    padding-left: 10px;
    box-sizing: border-box;
}
</style>


