		},
		methods: {
			choose(item, index) {
				item.choose = !item.choose;
				if(item.choose) {
					// 加入到数组中
					this.chooseList.push(item.name);
				}else {
					this.chooseList.forEach((chooseItem, index) => {
						if(chooseItem == item.name) {
							this.chooseList.splice(index, 1);
						}
					})
				}
//				console.log(this.chooseList);
			}
		}
	}
