<template>
	<div id="ProjectInfo" style="background-color: white;">
		<nav-header title="项目信息">
	        <span :class="{'disabled': disabled}"> <success-modal title="发布项目成功">发布项目</success-modal> </span>
		</nav-header>
		<hr />
		
		<div class="container">
			<input type="text" placeholder="项目名称" @keyup.enter="input" @keyup.delete="del" autofocus="autofocus"/>
			<input type="text" placeholder="描述您的项目" @keyup.enter="input" @keyup.delete="del"/>
			
			<div class="addGroup">
				<span class="add2"  @click="showAction">+</span>
				<span>添加附件</span>
			</div>
			
			<ul class="money">
				<li>
					<p>预算</p>
					<span class="coin" @click="show">{{coin1}} <img src="../../assets/img/icon-down2.png" style="width: 12px;" /></span>
					<div class="chooseDiv">
						<span class="choose" @click="choose1">人民币</span>
						<span @click="choose1">港币</span>
						<span @click="choose1">美元</span>
						<span @click="choose1">日元</span>
					</div>
					<p style="font-size: 19px;margin-top: -2px;border-bottom: 1px solid #dedede;">{{coin_symbol1}}<input type="number" value="80" class="price" @keyup.enter="input" @keyup.delete="del" /></p>
				</li>
				<li>
					<p>保证金</p>
					<span class="coin" @click="show">{{coin2}} <img src="../../assets/img/icon-down2.png" style="width: 12px;" /></span>
					<div class="chooseDiv">
						<span class="choose" @click="choose2">人民币</span>
						<span @click="choose2">港币</span>
						<span @click="choose2">美元</span>
						<span @click="choose2">日元</span>
					</div>
					
					<p style="font-size: 19px;margin-top: -2px;border-bottom: 1px solid #dedede;">{{coin_symbol2}}<input type="number" value="100" class="price" @keyup.enter="input" @keyup.delete="del" /></p>
					<p>为了保障双方权益，双方均需缴纳不低于预算10%的保证金</p>
				</li>
			</ul>
			
			<ul class="leibie">
				<h5>类别(最多5个)</h5>
				<li>
					<span class="add2" @click="$router.push({name: 'selectSkill'})">+</span>
					<span class="leibie-item">开发语言</span>
					<!--<a class="xiala">开源 <img src="../../assets/img/icon-down3.png" width="12"/></a>-->
				</li>
				<li>
					<span class="add2" @click="$router.push({name: 'selectSkill'})">+</span>
					<span class="leibie-item">框架</span>
				</li>
				<li>
					<span class="add2" @click="$router.push({name: 'selectSkill'})">+</span>
					<span class="leibie-item">适用行业</span>
				</li>
			</ul>
		</div>
		
		<!-- actionSheet-->
		<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
		
	</div>
</template>

<script>
import '@/assets/css/ActionSheet.css'
import NavHeader from '@/components/NavHeader4.vue'
import SuccessModal from '@/components/Modal/SuccessModal.vue'
	export default {
		data() {
			return {
				disabled: false,		// 是否禁用"发布项目"按钮
				actions: [
					{ "name": "相机" },
					{ "name": "相册" },
					{ "name": "文件" }
				],
				sheetVisible: false,
				coin1: "人民币",
				coin2: "人民币",
				coin_symbols1: [
					{ name: '人民币', symbol: 'CNY￥'},
					{ name: '港币', symbol: 'HK$'},
					{ name: '美元', symbol: '$'},
					{ name: '日元', symbol: 'JPY￥'}
				],
				coin_symbols2: [
					{ name: '人民币', symbol: 'CNY￥'},
					{ name: '港币', symbol: 'HK$'},
					{ name: '美元', symbol: '$'},
					{ name: '日元', symbol: 'JPY￥'}
				],
				coin_symbol1: 'CNY￥',
				coin_symbol2: 'CNY￥',
				
			}
		},
		components: {
			NavHeader,
			SuccessModal
		},
		mounted() {
			// 滚动到顶部
			$(window).scrollTop(0)
		},
		methods: {
			showAction() {
				this.sheetVisible = true;
			},
			input(event) {
				$(event.target).next().focus();
				if(event.target.type == 'number') {
					console.log($(event.target).parent().parent().next().children().children('input[type=number]').val())
					$(event.target).parent().parent().next().children().children('input[type=number]').focus();
				}
			},
			del(event) {
				// 功能：一直按着delete键向上删除
				if(event.target.value == "") {
					$(event.target).prev().focus();
					if(event.target.type == 'number') {
						$(event.target).parent().prev().children('input[type=number]').focus();
					}
				}
			},
			show(event) {
				$(event.target).next().slideToggle('fast');
			},
			choose1(event) {
				$(event.target).addClass('choose').siblings().removeClass('choose');
				this.coin1 = event.target.innerText;
				this.coin_symbols1.forEach((item,index) => {
					if(event.target.innerText == item.name) {
						this.coin_symbol1 = item.symbol;
					}
				})
				$('.chooseDiv').slideUp('fast');
			},
			choose2(event) {
				$(event.target).addClass('choose').siblings().removeClass('choose');
				this.coin2 = event.target.innerText;
				this.coin_symbols2.forEach((item,index) => {
					if(event.target.innerText == item.name) {
						this.coin_symbol2 = item.symbol;
					}
				})
				$('.chooseDiv').slideUp('fast');
			}
		}
	}
</script>

<style scoped="scoped">
header a {
	color: #0087e0;
}
hr {
	position: fixed;
    width: 100%;
    height: 2px;
    border: 0;
    background: #009cdd;
    top: 32px;
}
.container {
	padding: 50px 25px;
}
input {
	width: 100%;
    padding: 10px 0 10px 10px;
    border: 0;
    border-bottom: 1px solid #dedede;
    margin-top: 40px;
    box-sizing: border-box;
}
input[type=number] {
    padding: 0;
    margin: 0;
    border: 0;
    width: 50%;
    font-size: 19px;
}
.addGroup {
    margin-top: 50px;
}
.add2 {
	width: 20px;
    height: 20px;
    border: 2px solid #1e96ff;
    display: inline-block;
    border-radius: 50%;
    text-align: center;
    line-height: 22px;
    font-size: 22px;
    color: #1e96ff;
    vertical-align: middle;
}
.addGroup span:last-child {
	font-size: 12px;
    color: #1e96ff;
    font-weight: bold;
    margin-left: 10px;
}
.money {
	padding-top: 40px;
}
.money li {
	margin-bottom: 40px;
	position: relative;
}
.money p:first-child {
	font-size: 12px;
    color: #919191;
    padding-bottom: 14px;
    padding-left: 4px;
}
.money p:nth-child(3) {
	font-size: 19px;
    padding-bottom: 4px;
    margin-top: -2px;
}

.money span.coin {
    width: 72px;
	float: right;
	text-align: right;
	font-size: 14px;
    color: #0085dd;
    font-weight: bold;
    margin-top: -6px;
    padding: 6px 2px 6px 10px;
    box-sizing: border-box;
	border: 1px solid #e1e1e1;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}
.money span.coin.choosed {
   
}
.money span.choose2 {
	border: 1px solid #e1e1e1;
	border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}
.money p:last-child {
    font-size: 9px;
    margin-top: 5px;
    color: #080808;
}
.leibie li {
	padding: 10px 0;
    border-bottom: 1px solid #f0f0f0;
}
.leibie .add2 {
	border-color: #bbbbbb;
	color: #bbbbbb;
}
.leibie h5 {
	font-size: 12px;
	color: #919191;
	padding-bottom: 12px;
}
.leibie .leibie-item {
	font-size: 14px;
    font-weight: 600;
    padding-left: 20px;
}
.leibie li .xiala {
	font-size: 12px;
    float: right;
    margin-top: 6px;
    color: #7c7c7c;
}
.chooseDiv {
    float: right;
    position: absolute;
    top: 46px;
    right: 0;
    border: 1px solid #e1e1e1;
    width: 70px;
    background-color: white;
    display: none;
    z-index: 100;
    text-align: right;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top: 0;
}
.chooseDiv span{
	display: block;
    padding: 4px 16px 4px 5px;
    border-bottom: 1px solid #eee;
    color: #999;
    font-weight: normal;
    font-size: 14px;
}
.chooseDiv span:last-child {
	border-bottom: 0;
}
.chooseDiv span.choose {
	border-top: 1px solid #e1e1e1;
	border-radius: 0;
	color: white;
	background-color: #addaf6;
}
</style>


