<template>
	<div id="addPaymentCard" style="background-color: white;">
		<header class="mui-clearfix">
			<a @click="$router.go(-1)">
				<img class="mui-pull-left" src="../../assets/img/icon-left2.png" />
				<span class="mui-pull-left">银行卡</span>
			</a>
		</header>
		<div class="container">
			<img src="../../assets/img/paymentCard.png" />
			<p class="number">****  ****  ****  8585</p>
			<p class="addPaymentCard">
				<router-link :to="{name: 'addPaymentCardForm'}">+ 添加银行卡</router-link>
			</p>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				height:0
			}
		},
		mounted() {
			$('.container').css('height', (window.innerHeight-40));
		}
		
	}
</script>

<style scoped="scoped">
header {
	width: 100%;
	height: 40px;
	background-color: #0692ef;
	padding-top: 11px;
	box-sizing: border-box;
	padding-left: 25px;
}
header img {
	width: 10px;
	margin-top: 2px;
}
header span {
	font-size: 18px;
	color: white;
	margin-left: 3px;
	margin-top: 3px;
}
.container {
	height: 586px;
	padding: 25px 15px;
	background-color: #0087e0;
	box-sizing: border-box;
}
.container img {
	width: 100%;
}
.container .number {
	font-size: 1.3rem;
	color: white;
	text-align: center;
	position: relative;
	top: -10%;
}
.addPaymentCard {
	font-size: 18px;
	padding-bottom: 10px;
	border-bottom: 1px solid #b9def6;
}
.addPaymentCard a {
	color: #b9def6;
    display: block;
    width: 100%;
}
</style>


