<template>
	<footer>
		<div class="mint-tabbar is-fixed">
			<router-link :to="{name: 'Home'}" class="mint-tab-item">
				<div class="mint-tab-item-icon">
					<img src="../assets/img/info-active.png" v-if="active=='info'">
					<img src="../assets/img/info.png" v-if="infoIcon"/>
				</div>
				<div class="mint-tab-item-label blue" v-if="active=='info'">
					信息
				</div>
				<div class="mint-tab-item-label" v-if="infoIcon">
					信息
				</div>
			</router-link>
			<router-link :to="{name: 'Search'}" class="mint-tab-item">
				<div class="mint-tab-item-icon">
					<img src="../assets/img/search-active.png" v-if="active=='search'">
					<img src="../assets/img/search.png" v-if="searchIcon">
				</div>
				<div class="mint-tab-item-label blue" v-if="active=='search'">
					发现
				</div>
				<div class="mint-tab-item-label" v-if="searchIcon">
					发现
				</div>
			</router-link>
			<router-link :to="{name: 'MyProject'}" class="mint-tab-item">
				<div class="mint-tab-item-icon">
					<img src="../assets/img/myProject-active.png" v-if="active=='myProject'">
					<img src="../assets/img/myProject.png" v-if="myProjectIcon">
				</div>
				<div class="mint-tab-item-label blue" v-if="active=='myProject'">
					项目
				</div>
				<div class="mint-tab-item-label" v-if="myProjectIcon">
					项目
				</div>
			</router-link>
			<router-link :to="{name: 'introduction'}" class="mint-tab-item">
				<div class="mint-tab-item-icon">
					<img src="../assets/img/mine-active.png" v-if="active=='mine'">
					<img src="../assets/img/mine.png" v-if="mineIcon">
				</div>
				<div class="mint-tab-item-label blue" v-if="active=='mine'">
					我的
				</div>
				<div class="mint-tab-item-label" v-if="mineIcon">
					我的
				</div>
			</router-link>
		</div>
	</footer>
</template>

<script>
	export default {
		props: ['active'],
		data() {
			return {
				infoIcon: true,
				searchIcon: true,
				myProjectIcon: true,
				mineIcon: true,
			}
		},
		mounted() {
			// 初始化active类的指定
			switch (this.active){
				case 'info': this.infoIcon = false; break;
				case 'search': this.searchIcon = false; break;
				case 'myProject': this.myProjectIcon = false; break;
				case 'mine': this.mineIcon = false; break;
			}
		}
	}
</script>
<style>
footer .mint-tab-item-label{
	color: black;
}
footer .mint-tabbar {
	border-top: 1px solid #b3b3b3;
}
footer .mint-tab-item-label.blue {
	color: #0087e0;
}
</style>


