<template>
	<div style="background-color: white;">
		<nav-header title="" next="true"></nav-header>
		<hr />
		
		<div class="container">
			<h1>选择项目类型</h1>
			<p>我们这有两种方案供你管理项目</p>
			
			<div class="btn-group" @click="next">
				<img class="icon mui-pull-left" src="../../assets/img/icon-fixed.png" />
				<p class="title mui-pull-left">固定价格</p>
				<p class="des">推荐使用，限时交付</p>
			</div>
			<div class="btn-group" @click="next">
				<img class="icon mui-pull-left" src="../../assets/img/icon-clock.png" />
				<p class="title mui-pull-left">时薪制</p>
				<p class="des">推荐使用，可以变动</p>
			</div>
		</div>
	</div>
</template>

<script>
import NavHeader from '@/components/NavHeader4.vue'
	export default {
		data() {
			return {}
		},
		components: {
			NavHeader
		},
		methods: {
			next() {
				this.$router.push({name: 'hire_confirmPrice'});
			}
		}
	}
</script>

<style scoped="scoped">
hr {
	position: absolute;
    width: 25%;
    height: 2px;
    border: 0;
    background: #009cdd;
    top: 32px;
}
.container {
	padding: 80px 25px 0;
}	
.container h1 {
	font-size: 24px;
	font-family: "微软雅黑";
	font-weight: bold;
}
.container p {
	font-size: 15px;
    padding-top: 17px;
    font-family: "\9ED1\4F53";
    padding-bottom: 56px;
}
.btn-group {
	width: 100%;
	height: 130px;
	background-color: #1ea1f9;
    border-radius: 5px;
    margin-bottom: 10px;
}
p.title {
	font-size: 15px;
	color: white;
	padding-bottom: 20px;
}
.icon {
	width: 20px;
	margin: 17px 10px;
}
p.des {
    color: white;
    font-size: 13px;
    padding-top: 60px;
    text-align: center;
}
</style>


