<template>	<!--有title和右边的，返回为退后一步，没有border-bottom-->
	<div>
		<mt-header :title="title" class="navHeader2"> 
		  <a href="javascript:;" slot="left" @click="$router.go(-1)">
		    <mt-button icon="back">返回</mt-button>
		  </a>
		  <mt-button icon="more" slot="right"></mt-button>
		</mt-header>
	</div>
</template>

<script>
	export default {
		props: ['title'],
		data() {
			return {
			
			}
		}
	}
</script>



